<template>
  <div>
    <div class="col-md-8 col-md-offset-1 tighter-column">
      <div class="card-content">
        <div id="main-info">
          <img src="../../assets/img/noimg.png" alt="" id="initiator-avator">
          发布者：{{ initiator }}
        </div>
        <div id="wanted-title">{{ Title }}</div>
        <div id="info-detail" class="clearfix">
          <div id="coins">
            <img src="../../assets/img/credits.png" alt="">
            {{ coins }}
          </div>
          <div id="tags">
            <img src="../../assets/img/icon.png" >
            {{ tag }}
          </div>
          <div>
            <img src="../../assets/img/time.png">
            {{ duration }}
          </div>
        </div>
        <div id="comment">
          <p>{{ comment }}</p>
        </div>
        <div class="divider"></div>
        <button class="btn btn-primary center-block" id="upload" @click="Upload">
          <img src="../../assets/img/upload.png" alt="">
          应邀上传文档
        </button>
      </div>
    </div>
    <div class="col-md-2 tighter-column">
      <PersonPad :HasLoggedIn="HasLoggedIn" v-on:upload="Upload" v-on:login="Login" :PersonData="PersonData"></PersonPad>
      <PadOnOthers></PadOnOthers>
      <MostWanted></MostWanted>
    </div>
  </div>
</template>
<style scoped>
#main-info {
  font-size: 16px;
  line-height: 32px;
}
#initiator-avator {
  height: 32px;
  border: 1px #00A1E9 solid;
  border-radius: 16px;
}
#wanted-title {
  font-size: 24px;
  font-weight: bold;
  margin: 16px auto;
}
#info-detail {
  margin-bottom: 20px;
}
#info-detail>div {
  float: left;
  margin-bottom: 4px;
}
#info-detail img {
  width: 20px;
}
#time-img {
  width: 22px!important;/* 为了使三个 icon 在视觉上的大小一样 */
}
#coins, #tags {
  margin-right: 30px;
}
#comment {
  border-bottom: 1px solid #818181;
  font-size: 15px;
  line-height: 16px;
  margin-bottom: 50px;
}
#upload img {
  height: 16px;
}
#upload {
  border: 0;
  font-size: 20px;
  background-color: #01aff8;
  margin-bottom: 50px;
  color: white;
  width: 250px;
}
</style>
<script>
import $ from 'jquery'
import MostWanted from '../../components/common/MostWanted.vue' // “热门悬赏”板块
import PadOnOthers from '../../components/common/WantedLaunchPad/PadOnOthers.vue' // “发起悬赏”板块
import PersonPad from '../../components/common/PersonPad.vue'
export default {
  name: 'WantedDetail',
  components: { PersonPad, MostWanted, PadOnOthers },
  data () {
    return {
      initiator: 'avfav1235',
      Title: '电路原理下学期历年考题',
      coins: 20,
      tag: '高等数学',
      duration: '2018.11-2019.11',
      comment: '一些补充说明',
      WantedId: 0
    }
  },
  props: ['HasLoggedIn', 'PersonData'],
  mounted () {
    this.WantedId = this.$route.query.id
    document.body.scrollTop = 0
    document.documentElement.scrollTop = 0
    $.ajax({
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/getRewardDetail?id=' + this.WantedId,
      dataType: 'json',
      success: (data) => {
        this.Title = data.data.file_name
        this.comment = data.data.requirement
        this.coins = data.data.coin
        this.duration = data.data.start_time.split(' ')[0] + '至' + data.data.end_time.split(' ')[0]
        this.tag = data.data.tag_name
      }
    })
  },
  methods: {
    Upload: function () {
      this.$emit('upload', this.WantedId)
    },
    Login: function () {
      this.$emit('login')
    }
  }
}
</script>



