<template>
  <div class="row search-row">
    <div class="col-md-6 search-box col-md-offset-3">
      <div class="input-group">
        <input type="text" class="form-control iconfont" @keyup.13="Search" v-model="sbkeyword" :placeholder='msg'  id="search-input">
        <span class="input-group-btn btn" @click="Search">搜索文档</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'SearchBox',
  data () {
    return {
      sbkeyword: this.keyword, // 将 props 中 keyword 的值赋给 s(earch)b(ox)keyword，用于实现 props 的“双向绑定”
      msg: '\ue6ac输入关键词搜索'
    }
  },
  props: ['keyword'], // 接收来自父组件的值
  methods: {
    Search: function () {
      if (!this.sbkeyword) {
        alert('搜索内容不能为空')
      } else {
        this.$router.push({ path: '/search', query: { keyword: this.sbkeyword } })// 并不优雅，但似乎并不会造成页面刷新浪费资源？
        this.$emit('onsearch')
      }
    }
  },
  watch: {
    // 用于实现“双向绑定”
    keyword (arg) {
      this.sbkeyword = arg
    },
    sbkeyword (arg) {
      // 向父组件 Index.vue Searched.vue 传参
      this.$emit('getkeyword', arg)
    }
  }
}
</script>
<style scoped>
.search-row {
  margin: 0;
  padding-top: 10px;
}
.search-box {
  border-radius: 0;
  padding-left: 5px!important;
  padding-right: 5px!important;
}
.search-box>.input-group {
  width: 100%;
  border-radius: 20px;
  height: 40px;
}
.search-box input {
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;   /* 必要性存疑 */
  outline-color: invert;
  outline-style: none;
  outline-width: 0px;
  border: none;
  border-style: none;
  text-shadow: none;
  -webkit-appearance: none;
  /* -webkit-user-select: text; */
  outline-color: transparent;
  box-shadow: none;
}
.search-box .input-group-btn {
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: #00a0ea;
  color: white;
  border: 0;
  font-size: 18px;
  height: 40px!important;
}
.search-box span {
  border-radius: 0;   /* 必要性存疑 */
}
#search-input {
  outline: none;
  background-color: #f1f1f1;
  width: 100%;
  height: 40px;
  padding-left: 15px;
  border: 2px solid #00a0ea;
  padding-top: 8px;
}
#search-input:focus {
  outline: none !important;
  box-shadow: 0 !important;
  border-color: #ccc;
  z-index: 3;
  border: 2px solid #00a0ea;
}
@media screen and (max-width:780px) {
  .search-box .input-group-btn {
    padding: 6px 8px;
    font-size: 16px;
  }
}
</style>



