<template>
  <div>
    <SearchBox v-on:getkeyword="get" v-on:onsearch="OnSearch" :keyword="keyword"></SearchBox>
    <!-- 文件类型单选框 -->
    <div class="row file-type-container" style="width: 100%;">
      <div class="col-md-6 col-md-offset-3 col-sm-12 col-xs-12">
        <div class="radio search-type">
          <label class="col-md-2 col-sm-2 col-xs-2">
            <input type="radio" name="optionsRadios" v-model="FileType" id="optionsRadios1" value="ALL" checked><span>全部</span>
          </label>
          <label class="col-md-2 col-sm-2 col-xs-2">
            <input type="radio" name="optionsRadios" v-model="FileType" id="optionsRadios2" value="doc" ><span>DOC</span>
          </label>
          <label class="col-md-2 col-sm-2 col-xs-2">
            <input type="radio" name="optionsRadios" v-model="FileType" id="optionsRadios3" value="ppt"><span>PPT</span>
          </label>
          <label class="col-md-2 col-sm-2 col-xs-2">
            <input type="radio" name="optionsRadios" v-model="FileType" id="optionsRadios4" value="txt"><span>TXT</span>
          </label>
          <label class="col-md-2 col-sm-2 col-xs-2">
            <input type="radio" name="optionsRadios" v-model="FileType" id="optionsRadios5" value="pdf"><span>PDF</span>
          </label>
          <label class="col-md-2 col-sm-2 col-xs-2">
            <input type="radio" name="optionsRadios" v-model="FileType" id="optionsRadios6" value="xls"><span>XLS</span>
          </label>
        </div>
      </div>
    </div>
    <!-- 放弃实现复选框 -->
    <!-- <div class="tags-container">
      <div class="tags clearfix card" >
        <span class="related-tags">相关标签 <img src="../../assets/imgtag.png" id="related-tag"></span>
        <div class="clearfix btn-group" data-toggle="buttons">
          <button class="btn btn-primary tag">
            <input type="checkbox">高等数学
          </button>
          <button class="btn btn-primary tag">
            <input type="checkbox">期末复习
          </button>
          <button class="btn btn-primary tag">
            <input type="checkbox">线性代数
          </button>
        </div>
      </div>
    </div> -->
    <div class="body-center clearfix">
      <div id="results">
        <div class="card-content">
          <div id="search-filter-container">
            <div id="search-filter" class="pull-right">
              <span id="search-filter-hot" class="btn selected-filter" @click="FilterToggle($event)">热门</span>
              <span id="search-filter-new" class="btn" @click="FilterToggle($event)">最新</span>
            </div>
          </div>
          <div id="search-results">
            <div id="hot">
              <ul>
                <li id="result-items" v-for="file in Files" :key="file.id">
                  <img :src="TypeWrap(file)">
                  <div>
                    <router-link :to="{ path: '/file', query: { id: file.id } }">
                      <p id="searched-title">
                        <span class="pull-right">
                          <img src="../../assets/img/download.png" alt="">
                          <span>
                              {{ file.file_download }}
                          </span>
                        </span>
                        {{ file.file_title }}
                      </p>
                    </router-link>
                    <p>
                      <span>
                        上传时间：
                        <span id="upload-time">{{ UploadTime(file) }}</span>
                      </span>
                      <span>
                        文件大小：
                        <span id="file-size">{{ file.file_size }}</span>
                      </span>
                      <span>
                        上传者：
                        <span id="uploader">{{ file.user_name }}</span>
                      </span>
                      <span>
                        积分：
                        <span id="credits">{{ file.coin }}</span>
                      </span>
                    </p>
                  </div>
                </li>
              </ul>
              <div class="spinner" id="loader-hot" v-if="HotIsLoading">
                <div class="rect1"></div>
                <div class="rect2"></div>
                <div class="rect3"></div>
                <div class="rect4"></div>
                <div class="rect5"></div>
              </div>
            </div>
            <div id="recent" class="divhidden">
              <ul>
                <li id="result-items" v-for="file in FilesReverse" :key="file.id">
                  <img :src="TypeWrap(file)">
                  <div>
                    <router-link :to="{ path: '/file', query: { id: file.id } }">
                      <p id="searched-title">
                        <span class="pull-right">
                          <img src="../../assets/img/download.png" alt="">
                          <span>
                              {{ file.file_download }}
                          </span>
                        </span>
                        {{ file.file_title }}
                      </p>
                    </router-link>
                    <p class="clearfi">
                      <span>
                        上传时间：
                        <span id="upload-time">{{ UploadTime(file) }}</span>
                      </span>
                      <span>
                        文件大小：
                        <span id="file-size">{{ file.file_size }}</span>
                      </span>
                      <span>
                        上传者：
                        <span id="uploader">{{ file.user_name }}</span>
                      </span>
                      <span>
                        积分：
                        <span id="credits">{{ file.coin }}</span>
                      </span>
                    </p>
                  </div>
                </li>
              </ul>
              <div class="spinner" id="loader-recent" v-if="RecentIsLoading">
                <div class="rect1"></div>
                <div class="rect2"></div>
                <div class="rect3"></div>
                <div class="rect4"></div>
                <div class="rect5"></div>
              </div>
            </div>
            <div id="no-such-files" class="divhidden">
              <span>找不到相关文件<br>请尝试更换关键词搜索</span>
            </div>
            <div id="load-more" @click="LoadMore()" class="divhidden">
              <span>加载更多</span>
            </div>
          </div>
        </div>
      </div>
      <div id="searched-right" class="clearfix">
        <div id="personpad-container">
          <PersonPad :HasLoggedIn="HasLoggedIn" v-on:upload="Upload" v-on:login="Login" :PersonData="PersonData"></PersonPad>
        </div>
        <!-- “发起悬赏”板块 -->
        <PadOnSearched></PadOnSearched>
      </div>
    </div>
    <GoTop></GoTop>
  </div>
</template>
<style scoped>
