  width: 100%;
  background-color: #fff;
  height: auto;
  border-radius: 0;
  padding-top: 8px;
}
.tags button, #model-tag-left button {
  margin-bottom: 8px;
  margin-left: 10px;
  margin-right: 10px;
}
.tags label {
  margin-left: 20px!important;
  background-color: #f8f8f8;
  color: #616161;
  border: 0!important;
