}
.tags button, #model-tag-left button {
  margin-left: 8px!important;
  margin-right: 0!important;
  background-color: #f8f8f8;
  color: #616161;
  border: 0;
  border-radius: 0;
}
.tags label:hover {
  background-color: #67c4f0;
}
.tags label:focus {
  background-color: #67c4f0!important;
}
.tags label:active {
  background-color: #00a0ea!important;
}
