  background-color: #67c4f0;
}
.tags button:focus {
  background-color: #67c4f0!important;
  border: 0!important;
  border-radius: 0!important;
}
.tags button:active {
  background-color: #00a0ea!important;
}
.related-tags {
  padding-left: 10px;
  margin-top: 3px;
  width: 140px;
  float: left;
  font-size: 20px;
  border-left: 3px solid #00a0ea;
  display: block;
}
#related-tag {
  height: 17px;
  margin-left: 14px;
}
.tag.active {
  background-color: #67c4f0!important;
  z-index: 0 !important;
  border: 0!important;
  border-radius: 0!important;
}
#results {
  padding-left: 5px;
  padding-right: 5px;
  margin-bottom: 8px;
  margin-top: 10px;
}
#search-filter-container {
  width: 100%;
  display: block;
  height: 28px;
  border-bottom: 1px solid grey;
}
#search-filter {
  color: white;
  margin-top: -10px;
  margin-bottom: 10px;
  margin-right: 10px;
  height: 28px;
  width: 100px;
  border-radius: 5px;
  border: 2px solid #00A1E9;
  overflow: hidden;
  cursor: pointer;
}
#search-filter span:first-child{
  float: left;
  border-radius: 0;
  padding: 1px 10px;
  width: 50%;
  text-align: left;
  color: #00A1E9
}
#search-filter span:last-child{
  border-radius: 0;
  padding: 1px 10px;
  float: left;
  width: 50%;
