  margin: 10px 0;
  background-color: #fff;
  border-bottom: 1px solid rgba(128, 128, 128, 0.5);
  color: #616161;
}
#result-items div>p:last-child>span {
  display: inline-block;
  margin-top: 8px;
}
#result-items div>p:last-child {
  padding-left: 48px;
}
#result-items #upload-time, #result-items #uploader, #result-items #credits, #result-items #file-size {
  margin-right: 24px;
}
