  cursor: pointer;
  color: #252525!important;
}
#no-such-files {
  margin-top: 30px;
  color: #757575;
  text-align: center;
  font-size: 18px;
  margin-bottom: 30px;
}
#load-more {
  margin-top: 10px;
  color: #95999b;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  margin-bottom: -12px;
  transition: 0.3s
}
#load-more:hover {
  color: #00A1E9;
}
#searched-right {
  float: right;
}
#personpad-container {
  padding-left: 5px;
  padding-right: 5px;
  margin-top: 10px;
  margin-bottom: -8px;
  width: 100%;
}
@media screen and (min-width:1220px) {
  .body-center {
    margin: 0 auto;
    margin-top: 0;
    max-width: 1200px;
  }
  .tags {
    margin: 0 auto;
  }
  .tags label {
    width: 120px;
  }
  .tags button {
    width: 120px;
  }
  #results {
    width: 800px;
    margin-right: 10px;
    float: left;
  }
  #result-items>img:first-child {
    float: left;
    height: 28px !important;
    margin-right: 20px;
    margin-bottom: 20px;
    margin-top: 5px;
  }
  #searched-title {
    font-size: 18px;
  }
  #personpad-container {
    width: 390px;
    padding: 0!important;
    float: left;
  }
  #searched-right {
    width: 390px;
  }
}
@media screen and (max-width: 1220px) and (min-width:992px) {
  .body-center {
    margin: 0 auto;
    width: 910px;
  }
  #results {
    width: 70%;
    float: left;
  }
  #searched-right {
    width: 30%;
  }
  #personpad-container {
    float: left;
  }
}
@media screen and (min-width: 767px) and (max-width:992px) {
  .body-center {
    margin: 0 auto;
    width: 750px;
  }
  #results {
    width: 72%;
    float: left;
  }
  #searched-right {
    width: 28%;
  }
  #personpad-container {
    float: left;
  }
}
@media screen and (max-width:767px) {
  #related-tag {
    display: none;
  }
  .body-center {
    width: 100%;
    height: auto;
  }
}
@media screen and (max-width: 767px) {
  #personpad-container {
    width: 100%;
    margin-bottom: 10px;
    margin-top: 0;
  }
  #searched-right {
    width: 100%;
  }
}
</style>
<script>
import SearchBox from '../../components/common/SearchBox.vue' // 搜索框
import PadOnSearched from '../../components/common/WantedLaunchPad/PadOnSearched.vue' // “发起悬赏”板块
import GoTop from '../../components/GoTop.vue'
import PersonPad from '../../components/common/PersonPad.vue'
import $ from 'jquery'
export default {
  name: 'Searched',
  components: { SearchBox, PadOnSearched, GoTop, PersonPad },
  data () {
    return {
      CurrentPage: 1,
      HotIsLoading: true, // 标明“热门”是否正在加载
      HasNext: false, // 避免重复进行判断是否需要显示“加载更多”按钮的请求
      CurrentPage2: 1,
      RecentIsLoading: true, // 标明“最新”是否正在加载
      HasNext2: false, // 避免重复进行判断是否需要显示“加载更多”按钮的请求
      HasSearched: false,
      HasSearched2: false, // 避免在两个筛选器中连续切换造成重复请求并渲染的资源浪费
      keyword: '', // 搜索关键词
      Files: [], // 存储“热门”文件
      FilesReverse: [], // 存储“最新”文件
      FileType: 'ALL'
    }
  },
  props: ['HasLoggedIn', 'PersonData'],
  mounted () {
    // 接收关键词，以便使其出现在在输入框中
    this.keyword = this.$route.query.keyword
    $('#search-filter-container').removeClass('divhidden')
    $.ajaxSetup({ headers: { 'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content') } })
    $.ajax({
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/search?keyword=' + this.$route.query.keyword + '&num=15&orderBy=file_visit&orderType=desc&page=1',
      dataType: 'json',
      data: {
        '_token': '{{csrf_token()}}'
      },
      success: (data) => {
        this.HasSearched = true
        if (data.data.length === 0) {
          $('#no-such-files').removeClass('divhidden')
          $('#search-filter-container').addClass('divhidden')
        }
        this.Files = data.data
        this.HotIsLoading = false
        if (data.data.length < 15) {
          // 判断是否需要保留“加载更多”按钮
          $('#load-more').addClass('divhidden')
        }
        this.HasNextPage(1)
      }
    })
  },
  watch: {
    FileType: 'FilterByType'
  },
  methods: {
    get: function (arg) {
      // 用于接收 SearchBox 传出的参数
      this.keyword = arg
    },
    OnSearch: function () {
      // 用来接收 SearchBox 组件传来的“搜索”指令
      $('#hot').removeClass('divhidden')
      $('#recent').addClass('divhidden')
      $('#load-more').addClass('divhidden')
      this.HasSearched = true
      this.HasSearched2 = false
      this.HotIsLoading = true
      this.RecentIsLoading = true
      this.Files = []
      this.FilesReverse = []
      this.HasNext = false
      this.HasNext2 = false
      this.CurrentPage = 1
      this.CurrentPage2 = 1
      this.GetFiles()
    },
    GetFiles: function () {
      // 仅请求“热门”文件，“最新”文件在需要渲染时再请求
      $('#search-filter-container').removeClass('divhidden')
      $('#no-such-files').addClass('divhidden')
      $('#search-filter-hot').addClass('selected-filter')
      $('#search-filter-new').removeClass('selected-filter')
      $.ajax({
        type: 'GET',
        url: 'http://47.95.216.239/search?keyword=' + this.$route.query.keyword + '&num=15&orderBy=file_visit&orderType=desc&page=1' + (this.FileType !== 'ALL' ? ('&typeFilter=' + this.FileType) : ''),
        dataType: 'json',
        success: (data) => {
          this.HasSearched = true
          this.Files = data.data
          if (data.data.length === 0) {
            $('#no-such-files').removeClass('divhidden')
            $('#search-filter-container').addClass('divhidden')
          }
          this.HotIsLoading = false
          if (data.data.length < 15) {
            // 判断是否需要保留“加载更多”按钮
            $('#load-more').addClass('divhidden')
          }
          this.HasNextPage(1)
        }
      })
    },
    TypeWrap: function (data) {
      // 根据文件格式返回该格式对应的图标的路径以便渲染出 DOM
      // 不能用相对路径就很坑
      var TypeSrc = ''
      if (data.file_type === 'doc' || data.file_type === 'docx') {
        TypeSrc = '/static/FileImg/word.png'
      } else if (data.file_type === 'ppt' || data.file_type === 'pptx') {
        TypeSrc = '/static/FileImg/ppt.png'
      } else if (data.file_type === 'pdf') {
        TypeSrc = '/static/FileImg/pdf.png'
      } else if (data.file_type === 'xls') {
        TypeSrc = '/static/FileImg/xls.png'
      } else {
        TypeSrc = '/static/FileImg/default.png'
      }
      return TypeSrc
    },
    UploadTime: function (data) {
      // 返回年月日 但并不知道 yy.mm.dd 与 yy/mm/dd 与 yy-mm-dd 的各自的优劣
      var YMD = data.file_upload_time.split(' ')[0].split('-')
      return YMD.join('/')
    },
    LoadMore: function () {
      if ($('#search-filter-hot').hasClass('selected-filter')) {
        this.HotIsLoading = true
        this.CurrentPage += 1
        $.ajax({
          type: 'GET',
          url: 'http://47.95.216.239/search?keyword=' + this.$route.query.keyword + '&num=15&orderBy=file_visit&orderType=desc&page=' + this.CurrentPage + (this.FileType !== 'ALL' ? ('&typeFilter=' + this.FileType) : ''),
          dataType: 'json',
          success: (data) => {
            this.HotIsLoading = false
            this.Files = this.Files.concat(data.data)
            if (data.data.length < 15) {
              // 判断是否需要保留“加载更多”按钮
              $('#load-more').addClass('divhidden')
            }
            this.HasNextPage(1)
          }
        })
      }
      if ($('#search-filter-new').hasClass('selected-filter')) {
        this.RecentIsLoading = true
        this.CurrentPage2 += 1
        $.ajax({
          type: 'GET',
          url: 'http://47.95.216.239/search?keyword=' + this.$route.query.keyword + '&num=15&orderBy=file_upload_time&orderType=desc&page=' + this.CurrentPage2 + (this.FileType !== 'ALL' ? ('&typeFilter=' + this.FileType) : ''),
          dataType: 'json',
          success: (data) => {
            this.RecentIsLoading = false
            this.FilesReverse = this.FilesReverse.concat(data.data)
            if (data.data.length < 15) {
              // 判断是否需要保留“加载更多”按钮
              $('#load-more').addClass('divhidden')
            }
            this.HasNextPage(2)
          }
        })
      }
    },
    FilterToggle: function ($event) {
      var dom = $event.currentTarget
      if (!$(dom).hasClass('selected-filter')) {
        $(dom).addClass('selected-filter')
        $(dom).siblings().removeClass('selected-filter')
        $('#hot').toggleClass('divhidden')
        $('#recent').toggleClass('divhidden')
        if ($(dom).attr('id') === 'search-filter-new') {
          if (!this.HasSearched2) {
            // 若第一次点击“最新”
            this.HasSearched2 = true
            $.ajax({
              type: 'GET',
              url: 'http://47.95.216.239/search?keyword=' + this.$route.query.keyword + '&num=15&orderBy=file_upload_time&orderType=desc&page=' + this.CurrentPage2 + (this.FileType !== 'ALL' ? ('&typeFilter=' + this.FileType) : ''),
              dataType: 'json',
              success: (data) => {
                this.RecentIsLoading = false
                this.FilesReverse = data.data
                if (data.data.length < 15) {
                  // 判断是否需要保留“加载更多”按钮
                  $('#load-more').addClass('divhidden')
                }
                this.HasNextPage(2)
              }
            })
          } else { // 不是第一次点击最新
            if (this.HasNext2) $('#load-more').removeClass('divhidden')
            else $('#load-more').addClass('divhidden')
          }
        }
        if ($(dom).attr('id') === 'search-filter-hot') {
          if (!this.HasSearched) {
            this.GetFiles()
          }
          if (this.HasNext) $('#load-more').removeClass('divhidden')
          else $('#load-more').addClass('divhidden')
        }
      }
    },
    HasNextPage: function (index) {
      // 判断是否存在下一页以及 HasNext 的值，index用于区分是“热门”还是“最新”
      var RequestUrl
      if (index === 1) {
        RequestUrl = 'http://47.95.216.239/search?keyword=' + this.$route.query.keyword + '&num=15&orderBy=file_visit&orderType=desc&page=' + (this.CurrentPage + 1) + (this.FileType !== 'ALL' ? ('&typeFilter=' + this.FileType) : '')
      } else {
        RequestUrl = 'http://47.95.216.239/search?keyword=' + this.$route.query.keyword + '&num=15&orderBy=file_upload_time&orderType=desc&page=' + (this.CurrentPage2 + 1) + (this.FileType !== 'ALL' ? ('&typeFilter=' + this.FileType) : '')
      }
      $.ajax({
        async: true,
        type: 'GET',
        url: RequestUrl,
        dataType: 'json',
        success: (data) => {
          if (data.data.length) {
            if (index === 1) this.HasNext = true
            if (index === 2) this.HasNext2 = true
            $('#load-more').removeClass('divhidden')
          } else {
            if (index === 1) this.HasNext = false
            if (index === 2) this.HasNext2 = false
            $('#load-more').addClass('divhidden')
          }
        }
      })
    },
    FilterByType: function () {
      // 当用户点击了文件类型的单选框来进行筛选时调用。为避免多余的网络请求，仅请求“热门”“最新”二者中当前所处的那类的文件
      this.Files = []
      this.FilesReverse = []
      this.HasNext = false
      this.HasNext2 = false
      this.CurrentPage = 1
      this.CurrentPage2 = 1
      this.HasSearched = false
      this.HasSearched2 = false
      this.HotIsLoading = true
      this.RecentIsLoading = true
      $('#no-such-files').addClass('divhidden')
      $('#search-filter-container').removeClass('divhidden')
      $.ajax({
        type: 'GET',
        url: 'http://47.95.216.239/search?keyword=' + this.$route.query.keyword + (this.FileType !== 'ALL' ? ('&typeFilter=' + this.FileType) : '') + '&num=15&orderType=desc&page=1' + ($('#search-filter-hot').hasClass('selected-filter') ? '&orderBy=file_visit' : '&orderBy=file_upload_time'),
        dataType: 'json',
        success: (data) => {
          if ($('#search-filter-hot').hasClass('selected-filter')) {
            this.HotIsLoading = false
            this.Files = data.data
            this.HasSearched = true
          } else {
            this.RecentIsLoading = false
            this.FilesReverse = data.data
            this.HasSearched2 = true
          }
          if (data.data.length === 0) {
            $('#no-such-files').removeClass('divhidden')
            $('#search-filter-container').addClass('divhidden')
          }
          if (data.data.length < 15) {
            // 判断是否需要保留“加载更多”按钮
            $('#load-more').addClass('divhidden')
          }
          this.HasNextPage($('#search-filter-hot').hasClass('selected-filter') ? 1 : 2)
        }
      })
    },
    Upload: function () {
      this.$emit('upload')
    },
    Login: function () {
      this.$emit('login')
    }
  }
}
</script>



