<template>
  <div class="wanted-body wanted-row">
    <div class="col-md-3 hidden-sm hidden-xs col-md-offset-1 tighter-column">
      <HotDisc></HotDisc>
      <HotTags></HotTags>
    </div>
    <div class="col-md-5 tighter-column col-sm-8" >
      <div class="card-content">
        <div id="model-middle-title" class="clearfix">
          <span>
            讨论中心
          </span>
          <div class="input-group">
            <input type="text" class="form-control iconfont" :placeholder="holder" id="search_input">
            <span class="input-group-btn btn">搜索</span>
          </div>
        </div>
        <div id="discussion-middle-items">
          <div id="discussion-item" class="clearfix">
            <div class="clearfix">
              <span class="pull-right">
                <img src="../../assets/img/icon.png" alt="">
                <span>官方指南</span>
              </span>
              <router-link :to="{ path: '/discdetail' }">讨论区说明</router-link>
            </div>
            <p>
              <img src="../../assets/img/noimg.png" alt="">
              <span id="uploader">avfav1235</span>
              <span id="upload-time">
                发起时间：
                <span>2018/10/5</span>
              </span>
              <span class="pull-right">
                评论
                <span id="discussion-comment-num">
                  1
                </span>
              </span>
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="col-md-2 col-sm-4 tighter-column">
      <PersonPad :HasLoggedIn="HasLoggedIn" v-on:upload="Upload" v-on:login="Login" :PersonData="PersonData"></PersonPad>
      <HotDisc class="hidden-lg hidden-md"></HotDisc>
      <PadOnOthers></PadOnOthers>
    </div>
  </div>
</template>
<style scoped>
#model-middle-title {
  color: #00A1E9;
  font-size: 20px;
  margin-top: -8px;
  font-weight: 900;
  width: 100%;
  margin-bottom: 11px;
}
#model-middle-title>span {
  float: left;
  font-size: 24px;
  cursor: default;
}
#model-middle-title .input-group {
  width: 240px;
  float: right;
  border-radius: 20px;
}
#model-middle-title input {
  height: 36px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  font-weight: 100;
  background-color: #fff;
  font-size: 14px;
  padding-top: 7px;
  border: 3px solid #00A1E9;
}
#model-middle-title .input-group .input-group-btn {
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: #00A1E9;
  color: white;
  height: 36px!important;
  border: 0;
  width: 40px;
  font-size: 16px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
#discussion-item img {
  height: 18px;
}
#discussion-item>p img {
  height: 22px;
  border-radius: 13px;
  width: 22px;
  border: 0;
  margin-top: -4px;
}
#discussion-item>p {
  padding-top: 10px;
}
#discussion-item>p:last-child {
  font-size: 13px;
}
#discussion-item>div:first-child {
  font-size: 16px;
}
#discussion-item>p:first-child .pull-right span {
  background-color: #f8f8f8;
  display: inline-block;
  height: 32px;
  width: auto;
  padding: 6px 12px;
  font-size: 14px;
}
#discussion-item {
  margin-bottom: 8px;
  font-weight: 600;
  cursor: pointer;
}
#discussion-item a {
  color: #252525!important;
}
#discussion-item span {
  font-weight: 500;
}
#uploader {
  margin-right: 18px;
}
#upload-time {
  display: inline-block;
}
#discussion-comment-num {
  margin-left: 5px;
}
</style>
<script>
import HotDisc from '../../components/common/HotDisc.vue'
import HotTags from '../../components/common/HotTags.vue'
import PersonPad from '../../components/common/PersonPad.vue'
import PadOnOthers from '../../components/common/WantedLaunchPad/PadOnOthers.vue' // “发起悬赏”板块
export default {
  name: 'Discussion',
  components: { HotDisc, HotTags, PersonPad, PadOnOthers },
  data () {
    return {
      holder: '\ue6ac输入关键词搜索'
    }
  },
  props: ['HasLoggedIn', 'PersonData'],
  mounted () {
    document.body.scrollTop = 0
    document.documentElement.scrollTop = 0
  },
  methods: {
    Upload: function () {
      this.$emit('upload')
    },
    Login: function () {
      this.$emit('login')
    }
  }
}
</script>



