<template>
  <div id="header">
    <div class="header-div">
      <div class="header-container hidden-xs ">
        <div id="toindex" class="clearfi">
          <router-link :to="{ path: '/' }">
            <img src="../assets/img/Title.svg" alt="天外天" id="logo">
          </router-link>
          <p id="title"></p>
        </div>
      </div>
      <div class="mobile-headercontainer visible-xs-block">
        <div class="btn-group pull-left" v-if="CurrentPage!=='Personal'">
          <span class="dropdown-toggle" data-toggle="dropdown"><i class="iconfont">&#xe7ce;</i></span>
          <ul class="dropdown-menu" id="father-tag" role="menu">
            <li>
              <router-link :to="{ path: '/' }">首页</router-link>
            </li>
            <li class="divider"></li>
            <li v-for="Ftag in TagTree" :key="Ftag.id" class="dropdown-submenu">
              <a href="#" class="dropdown-toggl" data-toggle="dropdown" data-stopPropagation="true" @click="Stop($event)">{{ Ftag.name }}</a>
              <ul class="dropdown-menu">
                <li id="son-tag" v-for="Stag in Ftag.SonTag" :key="Stag.id"  class="dropdown-submenu">
                  <a href="#"  class="dropdown-toggl" data-toggle="dropdown" data-stopPropagation="true" @click="Stop($event)">{{ Stag.name }}</a>
                  <ul class="dropdown-menu " id="grandson-tag">
                    <li v-for="SStag in Stag.SonTag" :key="SStag.id">
                      <router-link :to="{ path: '/filterbytag', query: { Tag: SStag.id } }">{{ SStag.name }}</router-link>
                    </li>
                  </ul>
                </li>
              </ul>
            </li>
          </ul>
        </div>
        <div class="btn-group pull-left" v-if="CurrentPage==='Personal'">
          <span class="dropdown-toggle" data-toggle="dropdown">
            <i class="iconfont">&#xe7ce;</i>
          </span>
            <ul class="dropdown-menu" role="menu">
              <li>
                <router-link :to="{ path: '/' }">首页</router-link>
              </li>
              <li class="divider"></li>
              <li>
                <a index="1" @click="ToPage($event)">我的收藏</a>
              </li>
              <li>
                <a index="2" @click="ToPage($event)">已传文件</a>
              </li>
              <li>
                <a index="3" @click="ToPage($event)">我的下载</a>
              </li>
              <li>
                <a index="4" @click="ToPage($event)">我的浏览</a>
              </li>
              <li>
                <a index="5" @click="ToPage($event)">悬赏任务</a>
              </li>
              <li>
                <a index="6" @click="ToPage($event)">个人资料</a>
              </li>
              <li class="divider" v-if="HasLoggedIn"></li>
              <li>
                <a @click="Logout" v-if="HasLoggedIn">退出</a>
              </li>
            </ul>
          </div>
        <div class=" pull-right">
          <div class="btn-group">
            <img v-if="HasLoggedIn" id="login_logomoblie" class="header_right_imgmobile" data-toggle="dropdown" src="../assets/img/noimg.png"/>
            <span v-if="!HasLoggedIn" id="login_textmoblie" class="denglu" @click="LogIn"><a>登录</a></span>
            <ul class="dropdown-menu pull-right person-menu" role="menu">
              <li>
                <router-link :to="{ path: '/user' }" v-if="CurrentPage!=='Personal'">进入个人中心</router-link>
                <a @click="Upload" >上传我的资料</a>
                <a id="exit_host" @click="Logout">退出</a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
#header {
  width: 100%;
  background-color: #00A1E9;
  font-size:16.53px;
  color:#f9fdfe!important;
  height: 48px;
}
.header-div {
  height: 48px;
  margin: 0 auto;
  max-width:1200px;
  padding-top: 6px;
}
#toindex {
  padding-left: 5px;
  width: 230px;
}
#logo {
  height: 46px;
  margin-top: -4px;
}
#title {
  font-size: 23px;
  float: left;
  padding-top: 1px;
  color: white!important;
}
.btn-group {
  margin-top: 1px;
}
.btn-group i {
  font-size: 30px;
  margin-top: -7px;
  display: inline-block;
}
.header_right_imgmobile{
  border: 2px solid white;
  box-sizing: border-box;
  width:32px;
  height: 32px;
  border-radius: 50px;
  margin-top: -2px;
  margin-left: 5px;
  float: right;
}
.person-menu {
  margin-right: 0;
}
.person-menu li {
  text-align: center;
}
#login_logomoblie, #login_textmoblie {
  cursor: pointer;
}
.dropdown-toggle {
  cursor: pointer;
}
.mobile-headercontainer {
  padding-left: 5px;
  padding-right: 5px;
  margin-left: auto;
  margin-right: auto;
}
.mobile-headercontainer .dropdown-menu {
  min-width: 70px!important;
}
#father-tag a, #son-tag a, #grandson-tag a {
  font-size: 16px;
}
#father-tag .divider {
  margin: 2px 0!important;
}
#father-tag a {
  padding-left: 10px!important;
  padding-right: 10px;
}
#father-tag .dropdown-menu {
  border-top-left-radius: 6px!important;
}
.dropdown-submenu {
  position: relative;
}
.dropdown-submenu > .dropdown-menu {
  top: 0;
  left: 100%;
  margin-top: -6px;
  margin-left: -1px;
  -webkit-border-radius: 0 6px 6px 6px;
  -moz-border-radius: 0 6px 6px;
  border-radius: 0 6px 6px 6px;
}
.dropdown-submenu:hover > .dropdown-menu {
  display: block;
}
.dropdown-submenu:hover > a:after {
  border-left-color: #fff;
}
.dropdown-submenu.pull-left {
  float: none;
}
.dropdown-submenu.pull-left > .dropdown-menu {
  left: -100%;
  margin-left: 10px;
  -webkit-border-radius: 6px 0 6px 6px;
  -moz-border-radius: 6px 0 6px 6px;
  border-radius: 6px 0 6px 6px;
}

@media screen and (min-width:1220px) {
  .header-container {
    width: 1100px;
  }
  #toindex {
    padding-left: 0;
  }
}
@media screen and (max-width: 410px) {
  #father-tag a, #son-tag a {
    font-size: 15px;
  }
  #grandson-tag a {
    font-size: 15px;
  }
  #father-tag a {
    padding-left: 8px!important;
    padding-right: 8px;
  }
}
@media screen and (max-width: 375px) {
  #father-tag a, #son-tag a {
    font-size: 14px;
  }
  #grandson-tag a {
    font-size: 13px;
    padding-right: 1px!important;
  }
  #father-tag a {
    padding-left: 4px!important;
    padding-right: 4px;
  }
}
</style>
<script>
import $ from 'jquery'
export default {
  name: 'Header',
  data () {
    return {
      CurrentPage: ''
      /* TagTree: {} */
    }
  },
  props: ['HasLoggedIn', 'TagTree'], // 是否登录
  mounted () {
  },
  created () {
    // 每次页面 created 时获取此页的路由
    this.PageInit()
  },
  methods: {
    PageInit: function () {
      // 获取路由名称
      this.CurrentPage = this.$route.name
    },
    LogIn: function () {
      this.$emit('login')
    },
    ToPage: function ($event) {
      var dom = $event.currentTarget
      this.$emit('topersonpage', $(dom).attr('index'))
    },
    Upload: function () {
      this.$emit('upload')
    },
    ShowTags: function ($event) {
    },
    Stop: function ($event) {
      // 防止点击后多级标签菜单消失（bootstrap 的 feature）
      $event.stopPropagation()
    },
    Logout: function () {
      this.$emit('logout')
    }
  },
  watch: {
    '$route': 'PageInit'
  }
}
</script>



