<template>
  <div>
    <div class="col-md-8 col-md-offset-1 tighter-column">
      <div class="card-content">
        <div class="clearfix" id="file-container">
          <div id="uploader" class="clearfix divhidden">
            <img :src="avator" alt="">
            上传者：{{ uploader }}
          </div>
          <div id="title-container">{{ FileTitle }}</div>
          <div id="file-hidden" class="divhidden">
            <div id="info-container" class="clearfix">
              <div id="upload-time">
                上传时间：{{ FileInfo.file_upload_time }}
              </div>
              <div>
                文件大小：{{ FileInfo.file_size }}
              </div>
              <div id="credits">
                <img src="../../assets/img/credits.png" alt="">
                {{ FileInfo.coin }}
              </div>
              <div>
                下载次数：{{ FileInfo.file_download }}
              </div>
              <div>
                文件类型：{{ FileInfo.file_type }}
              </div>
            </div>
            <div id="message-container" class="">
              文件说明：{{ FileInfo.file_message }}
            </div>
            <div v-if="HasLoggedIn" id="button-container" class="clearfix">
              <button id="fav-btn" class="ripple pull-left" @click="ToggleFav"><span v-if="!HasFaved">收藏</span><span v-if="HasFaved">取消收藏</span></button><button id="download-btn" class="ripple pull-right" @click="Download">下载</button>
            </div>
          </div>
        </div>
        <div class="spinner " id="loader">
          <div class="rect1"></div>
          <div class="rect2"></div>
          <div class="rect3"></div>
          <div class="rect4"></div>
          <div class="rect5"></div>
        </div>
      </div>
    </div>
    <div class="col-md-2 tighter-column">
      <PersonPad :HasLoggedIn="HasLoggedIn" v-on:upload="Upload" v-on:login="Login" :PersonData="PersonData"></PersonPad>
      <HotFile></HotFile>
      <PadOnOthers></PadOnOthers>
    </div>
  </div>
</template>
<style scoped>
#title-container {
  font-size: 25px;
  font-weight: 600;
}
#info-container {
  margin: 6px 0;
  color: #454545;
  min-height: 34px;
}
#info-container div {
  float: left;
  margin-right: 30px;
  height: 32px;
  line-height: 32px;
}
#uploader {
  font-size: 16px;
  line-height: 32px;
  margin-bottom: 10px;
}
#uploader>img {
  height: 32px;
  border-radius: 50%;
  border: 1px solid #00A1E9;
  float: left;
  margin-right: 10px;
}
#credits>img {
  height: 14px;
  margin-top: -4px;
  margin-right: 6px;
}
#message-container {
  width: 100%;
  height: 120px;
  background-color: #f5f5f5;
  border: 1px solid #00A1E9;
  color: #252525;
  padding: 10px;
  border-radius: 1px;
}
#button-container {
  width: 250px;
  margin: 0 auto;
  padding: 10px;
}
#fav-btn, #download-btn {
  width: 110px;
  border-radius: 4px;
  background-color: #00A1E9;
  color: white;
  border: none;
  text-align: center;
  vertical-align: middle;
  padding-top: 8px;
  padding-bottom: 8px;
  display: inline-block;
  font-size: 16px;
}
</style>
<script>
import $ from 'jquery'
import HotFile from '../../components/common/HotFile.vue'
import PadOnOthers from '../../components/common/WantedLaunchPad/PadOnOthers.vue' // “发起悬赏”板块
import PersonPad from '../../components/common/PersonPad.vue'
export default {
  name: 'FileDetail',
  components: { HotFile, PadOnOthers, PersonPad },
  data () {
    return {
      FileTitle: '文件详情',
      avator: '/static/FileImg/noimg.png',
      uploader: 'avfav1235',
      credits: '20',
      UploadTime: '2017/10/29',
      FileId: 0,
      HasFaved: false, // 是否已收藏
      FileInfo: {}
    }
  },
  props: ['HasLoggedIn', 'PersonData'],
  mounted () {
    this.CreatePage()
  },
  watch: {
    '$route': 'CreatePage'
  },
  methods: {
    CreatePage: function () {
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
      $('#file-hidden').addClass('divhidden')
      this.FileId = this.$route.query.id
      $.ajax({
        async: true,
        type: 'GET',
        url: 'http://47.95.216.239/login',
        dataType: 'json',
        success: () => {
        }
      })
      $.ajax({
        async: true,
        type: 'GET',
        url: 'http://47.95.216.239/getFileDetail?id=' + this.FileId,
        dataType: 'json',
        xhrFields: {
          // withCredentials: true
        },
        crossDomain: true,
        success: (data) => {
          if (data.data !== '文件不存在') {
            this.FileInfo = data.data
            this.FileTitle = this.FileInfo.file_title
            this.uploader = data.data.user_name
            if (this.FileInfo.ifKeep === '未收藏') this.HasFaved = false
            $('#loader').addClass('divhidden')
            $('#uploader').removeClass('divhidden')
            $('#file-hidden').removeClass('divhidden')
          } else {
            $('#loader').addClass('divhidden')
            this.FileTitle = '文件不存在'
          }
        },
        error: (data) => {
          console.log(data)
        }
      })
    },
    LoadFile: function () {
      // Has this function ever been used?
      $.ajax({
        async: true,
        type: 'GET',
        url: 'http://47.95.216.239/getFileDetail?id=' + this.FileId,
        dataType: 'json',
        success: (data) => {
          this.FileInfo = data.data
          this.FileTitle = this.FileInfo.file_title
          this.uploader = data.data.user_name
          if (this.FileInfo.ifKeep === '未收藏') this.HasFaved = false
          console.log(data.data)
          $('#loader').addClass('divhidden')
          $('#uploader').removeClass('divhidden')
          $('#info-container').removeClass('divhidden')
          $('#message-container').removeClass('divhidden')
          $('#button-container').removeClass('divhidden')
        }
      })
    },
    Upload: function () {
      this.$emit('upload')
    },
    Login: function () {
      this.$emit('login')
    },
    TypeWrap: function (FileType) {
      // 根据文件格式返回该格式对应的图标的路径以便渲染出 DOM
      // 不能用相对路径就很坑
      var TypeSrc = ''
      if (FileType === 'doc' || FileType === 'docx') {
        TypeSrc = '/static/FileImg/word.png'
      } else if (FileType === 'ppt' || FileType === 'pptx') {
        TypeSrc = '/static/FileImg/ppt.png'
      } else if (FileType === 'pdf') {
        TypeSrc = '/static/FileImg/pdf.png'
      } else if (FileType === 'xls') {
        TypeSrc = '/static/FileImg/xls.png'
      } else {
        TypeSrc = '/static/FileImg/default.png'
      }
      return TypeSrc
    },
    ToggleFav: function () {
      $.ajax({
        async: true,
        type: 'GET',
        url: 'http://47.95.216.239/userAddKeep?file_id=' + this.FileId,
        dataType: 'json',
        success: (data) => {
          console.log(data)
        }
      })
      if (!this.HasFaved) {
        this.HasFaved = !this.HasFaved
        $.ajax({
          async: true,
          type: 'GET',
          url: 'http://47.95.216.239/userAddKeep?file_id=' + this.FileId,
          dataType: 'json',
          success: (data) => {
            if (data.data === 'success') alert(data.data)
          }
        })
      }
    },
    Download: function () {
      $.ajax({
        async: true,
        type: 'GET',
        url: 'http://47.95.216.239/download?id=' + this.FileId,
        dataType: 'json',
        success: (data) => {
        }
      })
    }
  }
}
</script>



