<template>
  <div id="discussion-center" class="body-center-sondiv">
    <div class="card-content">
      <div class="body-center-every-title">
        <router-link :to="{ path: '/discussion' }">
          <b>讨论区</b>
        </router-link>
      </div>
      <div class="discussion-item">
        <div class="discussion-title clearfix">
            <span class="discussion-tag">
              <i class="iconfont">&#xe6c5; </i>
              <span>官方指南</span>
            </span>
            <label>最新</label>
            <router-link :to="{ path: '/discdetail' }">讨论区说明</router-link>
        </div>
        <div>
          <span class="discussion-launcher">
            <span> </span>
            <span>发布者：</span>
            <span>avfav1235</span>
          </span>
          <div class="discussion-launch-time">
            <span>
              发布时间：
            </span>
            <span>
              2018/10/1
            </span>
          </div>
        </div>
      </div>
      <div class="discussion-item ">
        <div class="discussion-title clearfix">
          <label>最新</label>
          <a href="javascript:void(0);">一个假文件</a>
          <span class="discussion-tag">
            <i class="iconfont">&#xe6c5; </i>
            <span>生命科学导论</span>
          </span>
        </div>
        <div>
          <span class=" discussion-launcher">
            <span> </span>
            <span>发布者：</span>
            <span>一个假用户</span>
          </span>
          <span class="discussion-launch-time">
            <span>
              发布时间：
            </span>
            <span>
              1小时前
            </span>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
.body-center-sondiv{
  background-color: transparent;
  border-radius: 0px;
  float: left;
  padding-left: 5px;
  padding-right: 5px;
  margin-bottom: 10px;
  border-radius: 10px;
}
#discussion-center .body-center-every-title b {
  color: #00A1E9!important;
  transition: 0.3s
}
#discussion-center .body-center-every-title b:hover {
  color: #2fc4ff!important;
}
.discussion-item {
  height: 100px;
  padding-top: 25px;
}
.discussion-title label {
  font-size: 15.21px;
  border: 1px solid #00A1E9;
  color:#00A1E9;
  border-radius: 8px;
  padding: 0px 10px;
  margin-right: 10px;
  padding-left: 5px!important;
  padding-right: 5px!important;
}
.discussion-title a {
  color: #95999b;
  font-size: 15px;
  font-weight: 600;
}
.discussion-title b {
  font-size:15.21px ;
  color:#a8abac;
}
.discussion-tag {
  float: right;
  margin-left: 8px;
}
.discussion-launcher span:first-child {
  height: 25px;
  border-radius: 13px;
  width: 25px;
  border: 1px solid #616161;
  display: inline-block;
  margin-bottom: -6px;
}
.discussion-launch-time {
  float: right;
  margin-top: 3px;
}
.body-center-every-title b{
  float: left;
  margin-right: 15px;
  margin-bottom: 5px;
  cursor: default;
  color:#00A1E9!important;
  transition: 0.3s;
}
.body-center-every-title {
  background-color: white;
  font-weight: 200;
  font-size:22px;
  color:#00A1E9!important;
  margin-bottom: 8px;
  line-height: 30px;
  padding-bottom:3px;
  margin-top: -8px;
  overflow: auto;
  zoom: 1;
}
@media screen and (min-width:1220px) {
  .body-center-sondiv {
    width: 600px;
  }
}
@media screen and (max-width: 1220px) and (min-width:992px) {
  .body-center-sondiv {
    width: 455px;
  }
}
@media screen and (min-width: 780px) and (max-width:992px) {
  .body-center-sondiv {
    height: auto;
    width: 380px;
  }
}
@media screen and (max-width:780px) {
  .body-center-sondiv {
    margin-top: 5px;
    width: 100%;
    margin-bottom: 5px!important;
  }
}
</style>
<script>
export default {
  name: 'DiscBoard'
}
</script>



