}
#price-num {
  color: #dd5067;
}
#more {
  float: right;
}
#more {
  color: #00a0ea;
  cursor: pointer;
  margin-left: 8px;
  margin-top: 8px;
}
#wanted-item-middle {
  padding-top: 10px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.5);
}
#wanted-item-middle>p:first-child>a {
  font-size: 19px;
  color: #252525!important;
}
#wanted-item-middle>p:last-child>span {
  float: left;
  margin-top: 8px;
  margin-right: 20px;
}
#wanted-middle-title .input-group {
  width: 240px;
  float: right;
  border-radius: 20px;
}
#wanted-middle-title input {
  height: 36px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  font-weight: 100;
  background-color: #fff;
  font-size: 14px;
  padding-top: 7px;
  border: 3px solid #dd5067;
  outline: none;
}
#wanted-middle-title input:focus {
  border: 3px #dd5067 solid!important;
  outline: none!important;
  box-shadow: none!important;
}
#no-such-files {
  margin-top: 30px;
  color: #757575;
  text-align: center;
  font-size: 18px;
  margin-bottom: 30px;
}
#wanted-middle-title>.input-group>input::-webkit-input-placeholder {
  font-size: 14px;
}
#wanted-middle-title>.input-group>input:-moz-placeholder {
  font-size: 14px;
}
#wanted-middle-title>.input-group>input::-moz-placeholder {
  font-size: 14px;
}
#wanted-middle-title>.input-group>input:-ms-input-placeholder {
  font-size: 14px;
}
#wanted-middle-title .input-group .input-group-btn {
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: #dd5067;
  color: white;
  height: 36px!important;
  border: 0;
  width: 40px;
  font-size: 16px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
#wanted-middle-title {
  color: #dd5067;
  font-size: 20px;
  margin-top: -8px;
  font-weight: 900;
  width: 100%;
  margin-bottom: 11px;
}
#wanted-middle-title>span {
  float: left;
  cursor: default;
  font-size: 24px;
}
@media screen and (min-width:1220px) {
  #wanted-middle-title {
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: #fff;
  }
}
@media screen and (max-width:420px) {
  #wanted-middle-title .input-group {
    width: 100%;
    margin-top: 8px;
  }
}
</style>
<script>
import $ from 'jquery'
import MostWanted from '../../components/common/MostWanted.vue' // “热门悬赏”板块
import PadOnOthers from '../../components/common/WantedLaunchPad/PadOnOthers.vue' // “发起悬赏”板块
import PersonPad from '../../components/common/PersonPad.vue' // 个人信息板块
export default {
  name: 'WantedMain',
  components: { MostWanted, PersonPad, PadOnOthers },
  data () {
    return {
      holder: '\ue6ac输入关键词搜索',
      files: [],
      HasSearched: false,
      CurrentNum: 15,
      keyword: ''
    }
  },
  props: ['HasLoggedIn', 'PersonData'],
  mounted () {
    document.body.scrollTop = 0
    document.documentElement.scrollTop = 0
    $.ajax({
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/getHotReward?num=' + this.CurrentNum,
      dataType: 'json',
      success: (data) => {
        this.files = data.data
      }
    })
  },
  methods: {
    PostReward: function () {
      this.$emit('postreward')
    },
    Upload: function () {
      this.$emit('upload')
    },
    Login: function () {
      this.$emit('login')
    },
    Search: function () {
      if (this.keyword === '') {
        alert('关键词不能为空')
      } else {
        this.files = []
        $('#load-more').addClass('divhidden')
        this.HasSearched = true
        $('#no-such-files').addClass('divhidden')
        this.CurrentNum = 15
        $('#loader').removeClass('divhidden')
        $.ajax({
          async: true,
          type: 'GET',
          url: 'http://47.95.216.239/searchReward?keyword=' + this.keyword + '&num=' + this.CurrentNum,
          dataType: 'json',
          success: (data) => {
            if ((data.data.length === 0) || (data.data === '结果为空')) {
              $('#no-such-files').removeClass('divhidden')
              $('#load-more').addClass('divhidden')
            }
            if (data.data.length >= this.CurrentNum) $('#load-more').removeClass('divhidden')
            $('#loader').addClass('divhidden')
            this.files = data.data
          }
        })
      }
    },
    LoadMore: function () {
      this.CurrentNum += 15
      $('#loader').removeClass('divhidden')
      if (this.HasSearched === false) {
        $.ajax({
          async: true,
          type: 'GET',
          url: 'http://47.95.216.239/getHotReward?num=' + this.CurrentNum,
          dataType: 'json',
          success: (data) => {
            $('#loader').addClass('divhidden')
            this.files = data.data
            if (data.data.length <= this.CurrentNum) {
              $('#load-more').removeClass('divhidden')
            } else {
              $('#load-more').addClass('divhidden')
            }
          }
        })
      } else {
        $('#load-more').addClass('divhidden')
        $('#no-such-files').addClass('divhidden')
        $.ajax({
          async: true,
          type: 'GET',
          url: 'http://47.95.216.239/searchReward?keyword=' + this.keyword + '&num=' + this.CurrentNum,
          dataType: 'json',
          success: (data) => {
            if (data.data.length >= this.CurrentNum) $('#load-more').removeClass('divhidden')
            $('#loader').addClass('divhidden')
            if (data.data.length === 0) {
              $('#no-such-files').removeClass('divhidden')
              $('#load-more').addClass('divhidden')
            }
            this.files = data.data
          }
        })
      }
    }
  }
}
</script>



