                      <textarea id="file-textarea" rows="5" cols="50" placeholder="填写简介(填写内容不得超过50字)" maxlength="50"></textarea>
                    </div>
                    <div id="file-coins">
                      <i class="iconfont">&#xe633;</i>
                      <select id="coin-selector">
                        <option value="1">1 积分</option>
                        <option value="3">3 积分</option>
