                        <option value="15">15 积分</option>
                        <option value="20">20 积分</option>
                        <option value="8">8 积分</option>
                      </select>
                      <span>>>积分规则</span>
                    </div>
                    <div id="file-classify">
                      <i class="iconfont">&#xe643;</i>
                      <select >
                        <option value="0">-请选择-</option>
                        <option value="ques_years">历年假题</option>
                        <option value="class_ppt">教师课件</option>
                        <option value="homework">作业答案</option>
                      </select>
                    </div>
                    <div id="file-tags">
                      <i class="iconfont">&#xe624;</i>
                      <input type="text" name="coin" id="coin" class="divhidden"/>
                      <select id="ftag-selector" @change="FtagChange()">
                        <option value="0">-请选择-</option>
                        <option v-for="Ftag in TagTree" :key="Ftag.id" :value="Ftag.id">{{ Ftag.name }}</option>
                      </select>
                      <select id="stag-selector" v-if="FTag!==0" @change="StagChange()">
                        <option value="0">-请选择-</option>
                        <option v-for="Stag in TagTree[FTag-1].SonTag" :key="Stag.id" :value="Stag.SonIndex">{{ Stag.name }}</option>
                      </select>
                      <select id="sstag-selector" v-if="STag!==0">
                        <option value="0">-请选择-</option>
                        <option v-for="SStag in TagTree[FTag-1].SonTag[STag].SonTag" :key="SStag.id" :value="SStag.id">{{ SStag.name }}</option>
                      </select>
                    </div>
                    <div id="empty-block-2" v-if="STag===0"></div>
                  </form>
                  <button class="pull-right btn btn-secondary" id="upload-btn-1" @click="Upload">确认上传</button>
                </div>
                <div id="modal-bottom">
                  <div class="path-container">
                    <img :src="FileImg" id="file-img" v-if="FileSelected" >
                    <span class="path">未选择文件</span>
                    <button type="button" class="close" id="clear-file" @click="ClearFile" aria-hidden="true" v-if="FileSelected">&times;</button>
                  </div>
                  <img src="./assets/img/file-add.png" @click="SelectFile" class="select-file" title="选择文件">
                  <button class="pull-right btn btn-secondary" id="upload-btn-2" @click="Upload">确认上传</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="clearfix">
      <div class="col-md-12">
        <div class="modal fade" id="modal-container-2" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
          <div class="modal-dialog">
            <div class="modal-content clearfix">
              <div id="reward-modal-main" class="clearfix">
                <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
                <div id="reward-name" class="clearfix">
                  <i class="iconfont">&#xe617;</i>
                  <input type="text" id="reward-name-input" placeholder="悬赏文件名称" maxlength="13"/>
                </div>
                <div id="reward-message" class="clearfix">
                  <i class="iconfont">&#xe640;</i>
                  <textarea id="reward-textarea" rows="5" cols="50" placeholder="填写悬赏简介(填写内容不得超过50字)" maxlength="50"></textarea>
                </div>
                <div id="reward-coins">
                  <i class="iconfont">&#xe633;</i>
                  <select id="reward-coin-selector">
                    <option value="1">1 积分</option>
                    <option value="3">3 积分</option>
                    <option value="5">5 积分</option>
                    <option value="8">8 积分</option>
                    <option value="10">10 积分</option>
                    <option value="15">15 积分</option>
                    <option value="20">20 积分</option>
                  </select>
                  <span>>>积分规则</span>
                </div>
                <div id="reward-classify">
                  <i class="iconfont">&#xe643;</i>
                  <select >
                    <option value="0">-请选择-</option>
                    <option value="ques_years">历年假题</option>
                    <option value="class_ppt">教师课件</option>
                    <option value="homework">作业答案</option>
                  </select>
                </div>
                <div id="reward-ddl">
                  <i class="iconfont">&#xe735;</i>
                  <span>截止时间：</span><input type="date" name="" id="reward-time">
                </div>
                <div id="reward-tags">
                  <i class="iconfont">&#xe624;</i>
                  <input type="text" name="coin" id="coin" class="divhidden"/>
                  <select id="ftag-selector-2" @change="FtagChange2()">
                    <option value="0">-请选择-</option>
                    <option v-for="Ftag in TagTree" :key="Ftag.id" :value="Ftag.id">{{ Ftag.name }}</option>
                  </select>
                  <select id="stag-selector-2" v-if="FTag2!==0" @change="StagChange2()">
                    <option value="0">-请选择-</option>
                    <option v-for="Stag in TagTree[FTag2-1].SonTag" :key="Stag.id" :value="Stag.SonIndex">{{ Stag.name }}</option>
                  </select>
                  <select id="sstag-selector-2" v-if="STag2!==0">
                    <option value="0">-请选择-</option>
                    <option v-for="SStag in TagTree[FTag2-1].SonTag[STag2].SonTag" :key="SStag.id" :value="SStag.id">{{ SStag.name }}</option>
                  </select>
                </div>
                <button class="pull-right btn btn-secondary" id="reward-btn" @click="PostReward">发起悬赏</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <router-view class="main" v-on:upload="ModalUp1" v-on:login="LogIn" v-on:postreward="ModalUp2"
    :HasLoggedIn="HasLoggedIn" :PersonPage="PersonPage" v-on:logout="LogOut" :TagTree="TagTree"
    :PersonData="PersonData"/>
    <Footer class="footer"></Footer>
  </div>
</template>
<script>
import Header from './components/Header.vue'
import Footer from './components/Footer.vue'
import $ from 'jquery'
export default {
  components: { Header, Footer },
  data () {
    return {
      FileImg: '/static/FileImg/default.png',
      FileSelected: false,
      HasLoggedIn: false,
      PersonPage: 6,
      TagTree: [],
      FTag: 0, // 上传文件时第一级标签在 TagTree 中的数组下标 + 1
      STag: 0, // 上传文件时第二级标签在所在父标签的 SonTag 数组中的数组下标
      SSTag: 0, // 上传文件时第三极标签的 id
      FTag2: 0, // 发布悬赏时第一级标签在 TagTree 中的数组下标 + 1
      STag2: 0, // 发布悬赏时第二级标签在所在父标签的 SonTag 数组中的数组下标
      SSTag2: 0,
      RewardId: 0,
      PersonData: {}
    }
  },
  mounted () {
    $.ajaxSetup({
      headers: {
        'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')
      }
    })
    $.ajax({
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/login',
      dataType: 'json',
      success: (data) => {
      }
    })
    $.ajax({
      type: 'GET',
      url: 'http://47.95.216.239/allFatherTag', // 请求第一层标签
      dataType: 'json',
      success: (Fdata) => {
        this.TagTree = Fdata.data
        for (var i = 1; i <= Fdata.data.length; i++) {
          $.ajax({
            async: false,
            type: 'GET',
            url: 'http://47.95.216.239/getTagsByFatherTag?id=' + i, // 请求第二层标签
            dataType: 'json',
            success: (Sdata) => {
              this.TagTree[i - 1].SonTag = Sdata.data
              if (Sdata.data.length > 0) {
                for (var j = Sdata.data[0].id, k = 0; k < Sdata.data.length; j++, k++) {
                  $.ajax({
                    async: false,
                    type: 'GET',
                    url: 'http://47.95.216.239/getTagsByFatherTag?id=' + j,
                    dataType: 'json',
                    success: (SSdata) => {
                      this.TagTree[i - 1].SonTag[k].SonIndex = k
                      this.TagTree[i - 1].SonTag[k].SonTag = SSdata.data
                      if (SSdata.data.length === 0) this.TagTree[i - 1].SonTag[k].SonTag.push({ id: 9999, name: '无子标签' })
                    }
                  })
                }
              } else {
                // 若无子标签
                this.TagTree[i - 1].SonTag.push({ id: 9999, name: '无子标签' })
              }
            }
          })
        }
      }
    })
  },
  methods: {
    ModalUp1: function (RewardId) {
      this.FTag = 0
      this.STag = 0
      this.SStag = 0
      $('#ftag-selector option:selected').val(0)
      $('#stag-selector option:selected').val(0)
      $('#sstag-selector option:selected').val(0)
      // 显示上传文件模态框
      if (RewardId) {
        this.RewardId = RewardId
        $('#file-coins').addClass('divhidden')
      } else {
        this.RewardId = 0
        $('#file-coins').removeClass('divhidden')
      }
      $('#modal-container-1').modal({backdrop: 'static', keyboard: false})
    },
    SelectFile: function () {
      // 选择文件
      $('#file').click()
    },
    FillPath: function () {
      // 在模态框中显示文件路径
      var path = $('#file').val()
      $('.path-container').addClass('path-not-empty')
      $('.path').html(path)
      this.FileSelected = true
      var len = path.split('.').length // 字符串被分割后的数组的长度
      var postfix = path.split('.')[len - 1]
      this.FileImg = this.TypeWrap(postfix)
    },
    TypeWrap: function (postfix) {
      // 根据文件后缀返回该格式对应的图标的路径以便渲染出 DOM
      // 不能用相对路径就很坑
      var TypeSrc = ''
      if (postfix === 'doc' || postfix === 'docx') {
        TypeSrc = '/static/FileImg/word.png'
      } else if (postfix === 'ppt' || postfix === 'pptx') {
        TypeSrc = '/static/FileImg/ppt.png'
      } else if (postfix === 'pdf') {
        TypeSrc = '/static/FileImg/pdf.png'
      } else if (postfix === 'xls') {
        TypeSrc = '/static/FileImg/xls.png'
      } else {
        TypeSrc = '/static/FileImg/default.png'
      }
      return TypeSrc
    },
    ClearFile: function () {
      $('#file').val('')
      this.FileImg = '/static/FileImg/default.png'
      $('.path-container').removeClass('path-not-empty')
      $('.path').html('未选择文件')
      this.FileSelected = false
    },
    Upload: function () {
      if (!$('#file-name-input').val() || !$('#file-textarea').val() || !$('#file').val() || !$('#file-classify option:selected').val() || !$('#sstag-selector option:selected').val() || ($('#sstag-selector option:selected').val() === '0')) {
        alert('请填写完整信息，选择有效标签')
      } else {
        if ($('#sstag-selector option:selected').val() === '9999') {
          alert('请选择有效标签')
        } else {
          var form = new FormData(document.getElementById('csrf-token'))
          form.append('file_title', $('#file-name-input').val())
          form.append('file_message', $('#file-textarea').val())
          form.append('file', $('#file').val())
          form.append('file_classify', $('#file-classify option:selected').val())
          form.append('tag', $('#sstag-selector option:selected').val())
          if (this.RewardId !== 0) {
            form.append('answer_reward', this.RewardId)
            $.ajax({
              url: 'http://47.95.216.239/uploadAnswerReward',
              type: 'POST',
              data: form,
              processData: false,
              contentType: false,
              success: function (data) {
                console.log(data)
              },
              error: function (e) {
                console.log(e)
              }
            })
          } else {
            form.append('coin', $('#coin-selector option:selected').val())
            $.ajax({
              url: 'http://47.95.216.239/upload',
              type: 'POST',
              data: form,
              processData: false,
              contentType: false,
              success: function (data) {
                console.log(data)
              },
              error: function (e) {
                console.log(e)
              }
            })
          }
          $('#ftag-selector option:selected').val(0)
          $('#stag-selector option:selected').val(0)
          $('#sstag-selector option:selected').val(0)
          this.FTag = 0
          this.STag = 0
          this.SStag = 0
        }
      }
    },
    LogIn: function () {
      this.HasLoggedIn = true
      $.ajax({
        // 获取用户个人信息
        async: true,
        type: 'GET',
        url: 'http://47.95.216.239/userinfo?id=1020',
        dataType: 'json',
        success: (data) => {
          this.PersonData = data.data
        }
      })
      alert('登陆成功')
      $.ajax({
        // 登录
        async: true,
        type: 'GET',
        url: 'http://47.95.216.239/login',
        dataType: 'json',
        success: () => {
        }
      })
    },
    LogOut: function () {
      this.HasLoggedIn = false
      alert('已退出登录')
      $.ajax({
        async: true,
        type: 'GET',
        url: 'http://47.95.216.239/logout',
        dataType: 'json',
        success: () => {
        }
      })
    },
    ModalUp2: function () {
      // 显示发布悬赏模态框
      this.FTag2 = 0
      this.STag2 = 0
      this.SStag2 = 0
      $('#ftag-selector-2 option:selected').val()
      $('#stag-selector-2 option:selected').val(0)
      $('#sstag-selector option:selected').val(0)
      $('#modal-container-2').modal({backdrop: 'static', keyboard: false})
    },
    ToPersonPage: function (index) {
      this.PersonPage = index
    },
    PostReward: function () {
      var dt = new Date()
      var year = dt.getFullYear()
      var month = dt.getMonth() + 1
      month = month < 10 ? '0' + month : month
      var day = dt.getDate()
      day = day < 10 ? '0' + day : day
      var hour = dt.getHours()
      hour = hour < 10 ? '0' + hour : hour
      var min = dt.getMinutes()
      min = min < 10 ? '0' + min : min
      var sec = dt.getSeconds()
      sec = sec < 10 ? '0' + sec : sec
      console.log('start: ' + year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':' + sec)
      console.log('end: ' + $('#reward-time').val() + ' ' + hour + ':' + min + ':' + sec)
      console.log('tag: ' + $('#sstag-selector-2 option:selected').val())
      console.log('coin: ' + $('#reward-coin-selector option:selected').val())
      console.log('name: ' + $('#reward-name-input').val())
      console.log('message: ' + $('#reward-textarea').val())
      if (!$('#reward-name-input').val() || !$('#reward-textarea').val() || !$('#reward-classify option:selected').val() || !$('#reward-time').val() || !$('#sstag-selector-2 option:selected').val() || ($('#sstag-selector-2 option:selected').val() === '0')) {
        alert('请填写完整信息，选择有效标签')
      } else {
        if ($('#sstag-selector-2 option:selected').val() === '9999') {
          alert('请选择有效标签')
        } else {
          var form = new FormData(document.getElementById('csrf-token'))
          form.append('file_name', $('#reward-name-input').val())
          form.append('requirement', $('#reward-textarea').val())
          form.append('end_time', $('#reward-time').val() + ' ' + hour + ':' + min + ':' + sec)
          form.append('start_time', year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':' + sec)
          form.append('class', $('#reward-classify option:selected').val())
          form.append('tag', $('#sstag-selector-2 option:selected').val())
          form.append('coin', $('#reward-coin-selector option:selected').val())
          $.ajax({
            url: 'http://47.95.216.239/upload',
            type: 'POST',
            data: form,
            processData: false,
            contentType: false,
            success: function (data) {
              console.log(data)
            },
            error: function (e) {
              console.log(e)
            }
          })
          $('#ftag-selector-2 option:selected').val(0)
          $('#stag-selector-2 option:selected').val(0)
          $('#sstag-selector-2 option:selected').val(0)
          this.FTag2 = 0
          this.STag2 = 0
          this.SStag2 = 0
        }
      }
    },
    FtagChange: function () {
      this.FTag = $('#ftag-selector option:selected').val()
      if (this.FTag === 0) this.STag = 0
      $('#stag-selector option:selected').val(0)
      $('#sstag-selector option:selected').val(0)
    },
    StagChange: function () {
      this.STag = $('#stag-selector option:selected').val()
      $('#sstag-selector option:selected').val(0)
    },
    FtagChange2: function () {
      this.FTag2 = $('#ftag-selector-2 option:selected').val()
      $('#stag-selector-2 option:selected').val(0)
      $('#sstag-selector option:selected').val(0)
    },
    StagChange2: function () {
      this.STag2 = $('#stag-selector-2 option:selected').val()
      $('#sstag-selector-2 option:selected').val(0)
    }
  },
  watch: {
  }
}
</script>
