  float: left;
  background: #fff;
  padding: 24px;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}
#modal-right > .close, #reward-modal-main > .close {
  margin-top: -12px;
  margin-right: -12px
}
#file-name > i, #reward-name > i {
  float: left;
  margin-top: 4px;
  font-size: 20px;
}
#file-message, #reward-message {
  margin-top: 2px;
}
#file-coins {
  margin-top: -5px;
}
#file-message > i, #reward-message > i {
  display: block;
  height: 24px;
  margin-top: 5px;
  float: left;
  font-size: 20px;
}
#file-name input, #file-message textarea, #reward-name input, #reward-message textarea {
  margin-left: 20px;
}
#file-name-input, #reward-name-input {
  border: none;
  border-bottom: 1px solid #747474;
  color: #747474;
  outline-style: none;
  font-size: 17px;
  padding-bottom: 3px;
  width: 81%;
  border-radius: 0!important;
}
#file-textarea, #reward-textarea {
  color: #747474;
  line-height: 25px;
  background: url(./assets/img/line2.png) repeat;
  position: relative;
  overflow: auto;
  font-size: 16px;
  width: 81%;
  border: none;
  resize: none;
  outline-style: none;
}
#file-coins > i, #reward-coins > i, #file-tags > i, #reward-tags > i {
  height: 22px;
  float: left;
  font-size: 23px;
}
#file-tags > i, #reward-tags > i {
  font-size: 25px;
  color: #344657e0!important;
  margin-top: 1px;
  height: 80px;
}
#reward-tags > i {
  margin-top: 7px;
}
#file-coins select, #reward-coins select, #file-classify select, #reward-classify select {
  color: #747474;
  font-size: 14px;
  margin-top: 8px;
  margin-left: 18px;
}
#file-classify i, #reward-classify i {
  font-size: 19px;
  padding-left: 3px;
}
#file-classify select {
  margin-left: 15px;
}
#reward-classify select {
  margin-left: 15px;
  margin-top: 16px;
}
#file-tags select, #reward-tags select {
  color: #747474;
  font-size: 14px;
  margin-top: 8px;
  margin-left: 16px;
}
#ftag-selector-2 {
  margin-left: 16px!important;
  margin-top: 15px!important;
}
#empty-block-2 {
  height: 31px;
}
#stag-selector {
  margin-left: 17!important;
}
#file-coins span, #reward-coins span {
  font-size: 14px;
  color: #979690;
}
#upload-btn-1, #upload-btn-2 {
  background-color: #fff;
  border: 1px solid #00A1E9;
  border-radius: 6px;
  color: #00A1E9;
  font-weight: 600;
  font-weight: 600;
  width: 100px;
  font-size: 18px;
}
#upload-btn-1 {
  margin-top: 6px;
}
#upload-btn-2 {
  display: none;
  float: right;
}
#reward-btn {
  background-color: #f0ad4e;
  border: 0;
  margin-top: 10px;
  border-radius: 6px;
  height: 36px;
  color: #fff;
  font-weight: 600;
  font-weight: 600;
  width: 100px;
  font-size: 18px;
  padding-top: 4px;
}
#modal-bottom {
  display: none;
  float: left;
  width: 100%;
  height: 100%;
  background-color: #00A1E9;
  padding: 24px;
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
}
#reward-modal-main {
  border-left: 12px solid #f0ad4e;
  padding: 24px;
}
#reward-ddl {
  margin-top: 16px;
  color: #979690;
  font-size: 15px;
}
#reward-ddl i {
  color: #2c3e50;
  float: left;
  font-size: 29px;
  margin-left: -3px;
  margin-right: 15px;
  line-height: 24px;
}
.modal {
  padding-right: 0!important;
}
.ripple {
  position: relative;
}
.ripple:focus{
  outline: none;
}
.ripple:after {
  content: "";
  display: block;
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  pointer-events: none;
  background-color: #0079a8;
  background-repeat: no-repeat;
  background-position: 50%;
  opacity: 0;
  transition: all .2s;
}
.ripple:active:after {
  opacity: .3;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: 0s;
}
@-webkit-keyframes stretchdelay {
  0%, 40%, 100% { -webkit-transform: scaleY(0.4) }
  20% { -webkit-transform: scaleY(1.0) }
}
@keyframes stretchdelay {
  0%, 40%, 100% {
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }  20% {
    transform: scaleY(1.0);
    -webkit-transform: scaleY(1.0);
  }
}
@media screen and (max-width:780px) {
  #modal-left {
    display: none;
  }
  #modal-right {
    width: 100%;
    border-bottom-right-radius: 0;
    border-top-left-radius: 6px;
  }
  #modal-bottom {
    display: block;
  }
  #upload-btn-1 {
    display: none;
  }
  #upload-btn-2 {
    display: block;
  }
}
@media screen and (max-width:768px) {
  .tighter-column {
    margin-top: 4px;
  }
}
</style>



