  color: white;
}
.form_sex span {
  border-radius: 15px;
  margin-right: 10px;
  font-size: 16px;
  font-weight: bold;
  color:white;
  background-color:#c6c6c6 ;
  padding: 2px 10px;
}
.form-group {
  margin-left: 0;
  margin-right: 0;
}
.delete-mobile {
  margin-right: -4px;
  margin-top: -4px;
  margin-bottom: -10px;
  display: none;
  font-size: 19px;
}
.reward-toggle:active {
  background-color: #00A1E9;
  color: white;
}
#no-file-1, #no-file-2, #no-file-3, #no-file-4 {
  margin-top: 40px;
  color: #757575;
  text-align: center;
  font-size: 18px;
  margin-bottom: 40px;
}
#launched .pbody_right_pright {
  height: 65px;
}
#answered .pbody_right_pright {
  height: 65px;
}
@media screen and (max-width:780px) {
  #pages-container {
    padding-left: 5px;
  }
}
@media (max-width: 767px) {
  .form_button {
    margin-top: 9px;
  }
  .form_button_save{
    margin-left: 0;
  }
  .form_lable{
    margin-left: 15px;
  }
  #pbody_left {
    display: none;
  }
  .pbody {
    margin-top: 0;
    padding-left: 0;
  }
  #page1, #page2, #page3, #page4, #page5 {
    padding: 10px!important;
    padding-right: 4px!important;
    border: none!important;
  }
  .pbody_right_text_time {
    padding-left: 0;
    padding-right: 4px;
  }
  .pbody_right_text_title img {
    margin-left: 6px;
    width: 24px;
    height: 24px;
  }
  .pbody_right_text_title_middle {
    margin-left: 34px;
  }
  .up_file span {
    margin-right: 10px;
  }
  .time_year {
    font-size: 17px;
  }
  .time_month {
    font-size: 12px;
  }
  .delete-mobile {
    display: block;
  }
}
@media (max-width: 550px) {
  .delete {
    display: none;
  }
}
</style>
<script>
import $ from 'jquery'
export default {
  name: 'Personal',
  data () {
    return {
      UserName: 'Van Darkholme',
      Visits: [], // 浏览过的文件
      Keeps: [], // 收藏的文件
      Downloads: [], // 下载的文件
      Rewards: [], // 发起的悬赏
      Answers: [], // 应答的悬赏
      Uploads: [] // 上传的文件
    }
  },
  props: ['PersonPage', 'PersonData'],
  watch: {
    PersonPage: 'ChangePageMobile'
  },
  mounted () {
    if (this.PersonData.length === 0) {
      // 若获取个人信息失败
      this.PersonData = {
        'name': '用户名',
        'academic': '学院',
        'profession': '专业',
        'grade': '年级',
        'email': '邮箱',
        'gender': 2,
        'coin': '未知'
      }
    }
    $.ajax({
      // 用户收藏
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/userkeeps',
      dataType: 'json',
      success: (data) => {
        $('#loader-person-1').addClass('divhidden')
        if (data.status === 'error') {
          $('#no-file-1').removeClass('divhidden')
        } else {
          this.Keeps = data.data
        }
      }
    })
    $.ajax({
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/userUploads',
      dataType: 'json',
      success: (data) => {
        $('#loader-person-2').addClass('divhidden')
        if (data.status === 'error' || data.data === '您目前没有上传的文件') {
          $('#no-file-2').removeClass('divhidden')
        } else {
          this.Uploads = data.data
        }
      }
    })
    $.ajax({
      // 用户下载
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/userdownloads?id=1020',
      dataType: 'json',
      success: (data) => {
        $('#loader-person-3').addClass('divhidden')
        if (data.status === 'error') {
          $('#no-file-3').removeClass('divhidden')
        } else {
          this.Downloads = data.data
        }
      }
    })
    $.ajax({
      // 浏览文件
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/uservisits',
      dataType: 'json',
      success: (data) => {
        $('#loader-person-4').addClass('divhidden')
        if (data.data.length === 0) {
          $('#no-file-4').removeClass('divhidden')
        } else {
          this.Visits = data.data
        }
      }
    })
    $.ajax({
      // 已传悬赏
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/userRewards',
      dataType: 'json',
      success: (data) => {
        this.Rewards = data.data
      }
    })
  },
  methods: {
    ToggleColor: function ($event) {
      var dom = $event.currentTarget
      $(dom).parent().parent().toggleClass('show_color')
    },
    ChangePage: function ($event) {
      var dom = $event.currentTarget
      $('#page' + $(dom).attr('index')).removeClass('divhidden')
      $('#pages-container').children().not('#page' + $(dom).attr('index')).addClass('divhidden')
      $(dom).parent('span').parent().addClass('show_page')
      $(dom).parent('span').parent().siblings().removeClass('show_page')
    },
    FileHover: function ($event) {
      if (document.body.clientWidth >= 767) {
        var dom = $event.currentTarget
        // $(dom).toggleClass('file-hover')
        $(dom).children('.pbody_right_text_right').children('.delete').toggleClass('divhidden')
      }
    },
    ChangePageMobile: function () {
      // 用于在较窄屏幕尺寸下切换个人中心各板块
      for (var i = 1; i <= 6; i++) {
        $('#page' + i).addClass('divhidden')
      }
      $('#page' + this.PersonPage).removeClass('divhidden')
    },
    ToggleReward: function ($event) {
      var dom = $event.currentTarget
      $(dom).addClass('active')
      $(dom).siblings().removeClass('active')
      if ($(dom).hasClass('launched')) {
        $('#launched').removeClass('divhidden')
        $('#answered').addClass('divhidden')
      } else {
        $('#launched').addClass('divhidden')
        $('#answered').removeClass('divhidden')
      }
    },
    TypeWrap: function (postfix) {
      // 根据文件后缀返回该格式对应的图标的路径以便渲染出 DOM
      // 不能用相对路径就很坑
      var TypeSrc = ''
      if (postfix === 'doc' || postfix === 'docx') {
        TypeSrc = '/static/FileImg/word.png'
      } else if (postfix === 'ppt' || postfix === 'pptx') {
        TypeSrc = '/static/FileImg/ppt.png'
      } else if (postfix === 'pdf') {
        TypeSrc = '/static/FileImg/pdf.png'
      } else if (postfix === 'xls') {
        TypeSrc = '/static/FileImg/xls.png'
      } else {
        TypeSrc = '/static/FileImg/default.png'
      }
      return TypeSrc
    }
  }
}
</script>



