<template>
  <div id="GoTop">
    <div class="GoTop" v-show="Show" @click="GoTop"><img src="../assets/img/back-to-top.png" alt=""></div>
  </div>
</template>
<style scoped>
.GoTop {
  position: fixed;
  right: 16px;
  bottom: 40px;
  width: 40px;
  height: 40px;
  opacity: 0.9;
  border-radius: 40px;
  overflow: hidden;
}
.GoTop img {
  width: 40px;
  height: 40px;
  cursor: pointer;
}
</style>
<script>
export default {
  name: 'GoTop',
  data () {
    return {
      ScrollTop: '',
      Show: false
    }
  },
  methods: {
    handleScroll () {
      this.ScrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      if (this.ScrollTop > 500) {
        this.Show = true
      } else {
        this.Show = false
      }
    },
    GoTop () {
      let timer = null
      let _that = this
      cancelAnimationFrame(timer)
      timer = requestAnimationFrame(function fn () {
        if (_that.ScrollTop > 0) {
          _that.ScrollTop -= 300
          document.body.scrollTop = document.documentElement.scrollTop = _that.ScrollTop
          timer = requestAnimationFrame(fn)
        } else {
          cancelAnimationFrame(timer)
          _that.Show = false
        }
      })
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>



