<template>
  <div class="card" id="personpad">
    <div id="avatar-bg-personpad">
      <img src="../../assets/img/noimg.png" alt="avatar" id="avatar-personpad" class="center-block">
      <p v-if="!HasLoggedIn" id="username-personpad"><span @click="Login">登录</span></p>
      <p v-if="HasLoggedIn" id="username-personpad"><router-link :to="{ path: '/user' }">{{ PersonData.name }}</router-link></p>
    </div>
    <!-- 在模板页面使用 -->
    <div id="model-personal-info" v-if="(HasLoggedIn&&CurrentPage==='Model')||(HasLoggedIn&&CurrentPage==='Tag')||(HasLoggedIn&&CurrentPage==='FileDetail')||(HasLoggedIn&&CurrentPage==='Searched')">
      <div id="uploaded-num-model">
        <span>{{ PersonData.myUpload }}</span>
        <span>已上传</span>
      </div>
      <div id="personal-credits-model">
        <span>{{ PersonData.coin }}</span>
        <span>积分</span>
      </div>
      <button type="button" class="btn btn-primary upload center-block" @click="Upload">
        <img src="../../assets/img/upload.png" alt="" id="upload-icon">上传文件
      </button>
    </div>
    <!-- 在悬赏页面使用 -->
    <div id="wanted-personal-info" v-if="(HasLoggedIn&&CurrentPage==='Wanted')||(HasLoggedIn&&CurrentPage==='WantedDetail')">
      <div id="launched-num">
        <span>{{ WantedNum }}</span>
        <span>已悬赏</span>
      </div>
      <div id="uploaded-num">
        <span>{{ UploadedNum }}</span>
        <span>已上传</span>
      </div>
      <div id="personal-credits">
        <span>{{ PersonData.coin }}</span>
        <span>积分</span>
      </div>
      <button type="button" class="btn btn-primary upload center-block" @click="Upload">
        <img src="../../assets/img/upload.png" alt="" id="upload-icon">上传文件
      </button>
    </div>
    <div id="discussion-personal-info" v-if="(HasLoggedIn&&CurrentPage==='Discussion')||(HasLoggedIn&&CurrentPage==='DiscDetail')">
      <div id="posted-num">
        <span>0</span>
        <span>发帖</span>
      </div>
      <div id="commented-num">
        <span>0</span>
        <span>评论</span>
      </div>
      <button type="button" class="btn  discussion-launch center-block">
        发起讨论
      </button>
    </div>
  </div>
</template>
<style scoped>
#personpad {
  margin-bottom: 10px;
}
#avatar-bg-personpad {
  height: 150px;
  background-color: #dcdcdc;
  padding-top: 10px;
  color: #252525;
}
#avatar-bg-personpad a {
  color: #252525;
}
#avatar-personpad {
  margin-top: 10px;
  box-sizing: border-box;
  height: 80px;
  width: 80px;
  border-radius: 40px;
  border: 2px solid #00a0ea;
  -moz-border-radius: 50px;
  -webkit-border-radius: 55px;
}
#username-personpad {
  font-size: 20px;
  margin: 5px auto;
  text-align: center;
  cursor: pointer;
}
#launched-num, #uploaded-num, #personal-credits, #uploaded-num-model, #personal-credits-model {
    float: left;
}
#launched-num>span, #uploaded-num>span, #personal-credits>span, #uploaded-num-model>span, #personal-credits-model>span {
  display: block;
  text-align: center;
  padding-bottom: 4px;
}
#launched-num>span:first-child, #uploaded-num>span:first-child, #personal-credits>span:first-child, #uploaded-num-model>span:first-child, #personal-credits-model>span:first-child {
  color: #dd5067;
}
#launched-num {
  width: 30%;
}
#uploaded-num {
  width:40%;
  border-left: 1px solid #616161;
  border-right: 1px solid #616161;
}
#uploaded-num-model {
  width: 50%;
  border-right: 1px solid #616161;
}
#personal-credits {
  width: 30%;
}
#personal-credits-model {
  width: 50%;
}
#wanted-personal-info, #model-personal-info, #discussion-personal-info {
  background-color: #fff;
  padding-top: 8px;
  height: 130px;
}
#wanted-personal-info button, #model-personal-info button, #discussion-personal-info button {
  margin-top: 60px;
  height: 36px;
  padding-top: 4px;
}
#upload-icon {
  height:18px;
}
.upload {
  border: 0;
  font-size: 20px;
  background-color: #01aff8;
  margin-top: 10px
}
#posted-num, #commented-num {
  float: left;
  width: 50%;
}
#posted-num>span, #commented-num>span {
  display: block;
  text-align: center;
  padding-bottom: 4px;
}
.discussion-launch {
  background-color: #01aff8;
  border: 0;
  color: white!important;
  font-size: 17px;
  width: 120px;
}
@media screen and (max-width:992px) {
  #avatar-personpad {
    height: 56px;
    width: 56px;
    border-radius: 50%;
    margin-top: 6px;
  }
  #avatar-bg-personpad {
    height: 120px;
  }
  #wanted-personal-info, #model-personal-info, #discussion-personal-info {
    height: 110px;
  }
  #wanted-personal-info button, #model-personal-info button, #discussion-personal-info button {
    margin-top: 56px;
  }
}
</style>
<script>
export default {
  name: 'PersonPad',
  data () {
    return {
      CurrentPage: '',
      ImgDefault: '../../assets/img/noimg.png',
      Name: '用户名',
      WantedNum: '0',
      UploadedNum: '0',
      Credits: '0'
    }
  },
  props: ['HasLoggedIn', 'PersonData'],
  created () {
    // 每次页面 created 时获取此页的路由
    this.PageInit()
  },
  methods: {
    PageInit: function () {
      // 获取路由名称
      this.CurrentPage = this.$route.name
    },
    Upload: function () {
      this.$emit('upload')
    },
    Login: function () {
      this.$emit('login')
    }
  },
  watch: {
    '$route': 'PageInit'
  }
}
</script>



