<template>
  <div class="card-content"  id="most-wanted">
    <div id="wanted-left-title">
      <p>
        <span>热门</span>悬赏
      </p>
    </div>
    <div class="wanted-item-left" v-for="file in MostWanted" :key="file.id">
      <p><router-link :to="{ path: '/wanteddetail', query: { id: file.id } }">{{ file.file_name }}</router-link></p>
      <p id="wanted-item-detail">发起用户：
        <span id="sponsor">{{ file.user_name }}</span>
        <span id="price">悬赏积分</span>
        <span id="price-num">{{ file.coin }}</span>
      </p>
    </div>
  </div>
</template>
<style scoped>
#most-wanted {
  margin-bottom: 10px;
}
#wanted-left-title span {
  color: #dd5067;
  font-weight: 600;
}
#wanted-left-title>p {
  border-left: 2px solid #dd5067;
  padding-left: 5px;
  margin-bottom: 20px;
}
.wanted-item-left {
  color: #616161;
  background-color: #fff;
  margin: 10px 10px;
  margin-left: 0;
  border-bottom: 1px solid grey;
}
.wanted-item-left p a {
  color: #616161!important;
}
#wanted-item-detail {
  font-size: 10px;
}
@media screen and (min-width:1220px) {
  #wanted-left-title>p {
    margin-bottom: 0;
    font-size: 16px;
  }
  #wanted-left-title {
    padding: 10px 10px;
    background-color: #fff;
  }
}
</style>
<script>
import $ from 'jquery'
export default {
  name: 'MostWanted',
  data () {
    return {
      MostWanted: []
    }
  },
  mounted () {
    document.body.scrollTop = 0
    document.documentElement.scrollTop = 0
    $.ajax({
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/getHotReward?num=3',
      dataType: 'json',
      success: (data) => {
        this.MostWanted = data.data
      }
    })
  }
}
</script>



