<template>
  <div class="body-center-sondiv">
    <div class="card-content">
      <div class="body-center-every-title">
        <!-- 通过 query 传值链接到各自对应的模板页 -->
        <router-link :to="{ path: '/mainpage', query: { PageTitle: BoardTitles[BoardIndex] } }"><b>{{ ChineseTitles[BoardIndex] }}</b></router-link>
      </div>
      <div class="spinner loader">
        <div class="rect1"></div>
        <div class="rect2"></div>
        <div class="rect3"></div>
        <div class="rect4"></div>
        <div class="rect5"></div>
      </div>
      <div class="body-center-every-text" v-for="file in files" :key="file.id">
        <ul class="body-center-every-text-ul">
          <li><img :src="TypeWrap(file)"/></li>
          <li><span><router-link :to="{ path: '/file', query: { id: file.id } }">{{ file.file_title }}</router-link></span>
            <p>
              <i class="iconfont">&#xe6e6; </i>
              {{ file.file_visit }}
              <i class="iconfont">&#xe714; </i>
              {{ file.file_download }}
            </p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<style scoped>
.body-center-sondiv{
  background-color: transparent;
  border-radius: 0px;
  float: left;
  padding-left: 5px;
  padding-right: 5px;
  margin-bottom: 10px;
  border-radius: 10px;
}
.body-center-every-title {
  background-color: white;
  font-weight: 200;
  font-size:22px;
  color:#00A1E9!important;
  margin-bottom: 8px;
  line-height: 30px;
  padding-bottom:3px;
  margin-top: -8px;
  overflow: auto;
  zoom: 1;
}
.body-center-every-title b{
  float: left;
  margin-right: 15px;
  margin-bottom: 5px;
  cursor: default;
  color:#00A1E9!important;
  transition: 0.3s;
}
.body-center-every-title b:hover {
  color:#48cbff!important;
}
.body-center-every-text {
  margin-top:-10px;
  margin-left:0px;
  background-color: #ffffff;
  padding-top: 13px;
  float: left;
}
.body-center-every-text-ul p{
  margin-top: 6px;
}
.body-center-every-text-ul {
  padding-left: 0;
  cursor: default;
}
.body-center-every-text-ul span {
  font-weight: 900;
  display: inline-block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.body-center-every-text-ul i{
  font-weight: bold;
}
.body-center-every-text-ul i:last-child{
  margin-left: 10px;
}
.body-center-every-text li{
  display: inline-block;
  list-style: none;
  font-weight: 500;
  font-size:15px;
  color:#95999b;
  margin-right: 10px;
}
.body-center-every-text a{
  color:#95999b;
}
.body-center-every-text img{
    margin-top: -40px;
}
@media screen and (min-width:1220px) {
  .body-center-every-text-ul>li>img {
    height: 30px;
    border-radius: 15px;
  }
  .body-center-sondiv {
    width: 600px;
  }
  .body-center-every-text {
    width: 271px;
  }
}
@media screen and (max-width: 1220px) and (min-width:992px) {
  .body-center-sondiv {
    width: 455px;
  }
  .body-center-every-text-ul>li>img {
    height: 20px;
    border-radius: 10px;
  }
  .body-center-every-text {
    width: 197px;
  }
  .body-center-every-text-ul span {
    width: 152px;
  }
}
@media screen and (min-width: 780px) and (max-width:992px) {
  .body-center-sondiv {
    height: auto;
    width: 380px;
  }
  .body-center-every-text-ul>li>img {
    height: 30px;
    border-radius: 15px;
  }
  .body-center-every-text {
    width: 322px;
  }
}
@media screen and (max-width:780px) {
  .body-center-every-text-ul>li>img {
    height: 30px;
    border-radius: 15px;
  }
  .body-center-sondiv {
    margin-top: 5px;
    width: 100%;
    margin-bottom: 5px!important;
  }
  .body-center-every-text {
    width: 100%;
  }
}
</style>
<script>
import $ from 'jquery'
export default {
  // 用于主页的四个主要板块
  name: 'MainBoard',
  data () {
    return {
      RequestUrl: '',
      ChineseTitles: [
        '热门资源',
        '作业答案',
        '历年假题',
        '教师课件'
      ],
      BoardTitles: [
        'hot',
        'homework',
        'exams',
        'courseware'
      ],
      files: []
    }
  },
  props: ['BoardIndex'],
  mounted () {
    this.files = []
    if (this.BoardIndex === 0) {
      this.RequestUrl = 'http://47.95.216.239/getHotList?num=6'
    } else if (this.BoardIndex === 1) {
      this.RequestUrl = 'http://47.95.216.239/getHotHomeworkList?num=6'
    } else if (this.BoardIndex === 2) {
      this.RequestUrl = 'http://47.95.216.239/getHotQuesYearsList?num=6'
    } else if (this.BoardIndex === 3) {
      this.RequestUrl = 'http://47.95.216.239/getHotClassPptList?num=6'
    }
    $.ajax({
      async: true,
      type: 'GET',
      url: this.RequestUrl,
      dataType: 'json',
      success: (data) => {
        this.files = data.data
        if (data.status === 'success') $('.loader').addClass('divhidden')
      }
    })
  },
  methods: {
    TypeWrap: function (data) {
      // 根据文件格式返回该格式对应的图标的路径以便渲染出 DOM
      // 不能用相对路径就很坑
      var TypeSrc = ''
      if (data.file_type === 'doc' || data.file_type === 'docx') {
        TypeSrc = '/static/FileImg/word.png'
      } else if (data.file_type === 'ppt' || data.file_type === 'pptx') {
        TypeSrc = '/static/FileImg/ppt.png'
      } else if (data.file_type === 'pdf') {
        TypeSrc = '/static/FileImg/pdf.png'
      } else if (data.file_type === 'xls') {
        TypeSrc = '/static/FileImg/xls.png'
      } else {
        TypeSrc = '/static/FileImg/default.png'
      }
      return TypeSrc
    }
  }
}
</script>



