<template>
  <div class="wanted-body wanted-row">
    <div class="col-md-3 hidden-sm hidden-xs col-md-offset-1 tighter-column">
      <!-- “热门资源”板块  -->
      <HotFile v-if="!IsHotFile"></HotFile>
      <!-- 作业答案板块 -->
      <HomeworkPad v-if="IsHotFile"></HomeworkPad>
      <!-- “热门标签”板块 -->
      <HotTags></HotTags>
    </div>
    <div class="col-md-5 tighter-column col-sm-8" >
      <div class="card-content">
        <div id="model-middle-title" class="clearfix">
          <span>
            {{ ModelTitle }}
          </span>
            <span v-if="CurrentPage==='Tag'">
              标签下的内容
            </span>
          <div class="input-group" v-if="CurrentPage==='Model'">
            <input type="text" class="form-control iconfont" :placeholder="holder" @keyup.13="Search" v-model="keyword" id="search_input">
            <span class="input-group-btn btn" @click="Search">搜索</span>
          </div>
        </div>
        <div id="no-such-files" class="divhidden">
          <span>无相关文件</span>
        </div>
        <div id="model-middle-items" class="clearfix" v-for="file in files" :key="file.id">
          <img :src="TypeWrap(file)">
          <div id="">
            <p>
              <span class="pull-right">
                <img src="../../assets/img/credits.png" alt="">
                <span>{{ file.coin }}</span>
              </span>
              <router-link :to="{ path: '/file', query: { id: file.id } }" class="file-name">{{ file.file_title }}</router-link>
            </p>
            <p>
              上传时间：<span id="upload-time">{{ UploadTime(file) }}</span>
              <span>
                上传者：
                <span id="uploader">{{ file.user_name }}</span>
              </span>
              <span class="pull-right">
                <img src="../../assets/img/download.png" alt="">
                <span>
                    {{ file.file_download }}
                </span>
              </span>
            </p>
          </div>
        </div>
        <div class="spinner" id="loader">
          <div class="rect1"></div>
          <div class="rect2"></div>
          <div class="rect3"></div>
          <div class="rect4"></div>
          <div class="rect5"></div>
        </div>
        <div id="load-more"  @click="LoadMore()" class="divhidden">
          <span>加载更多</span>
        </div>
      </div>
    </div>
    <div class="col-md-2 col-sm-4 tighter-column">
      <!-- 个人信息板块 -->
      <PersonPad :HasLoggedIn="HasLoggedIn" v-on:upload="Upload" v-on:login="Login" :PersonData="PersonData"></PersonPad>
      <!-- “热门资源”板块，仅在屏幕尺寸为 sm 时显示  -->
      <HotFile v-if="!IsHotFile" class="hidden-lg hidden-md"></HotFile>
      <!-- 作业答案板块，仅在屏幕尺寸为 sm 时显示 -->
      <HomeworkPad v-if="IsHotFile" class="hidden-lg hidden-md"></HomeworkPad>
      <!-- “热门标签”板块，仅在屏幕尺寸为 sm 时显示 -->
      <HotTags class="hidden-lg hidden-md"></HotTags>
      <!-- “发起悬赏”板块 -->
      <PadOnOthers></PadOnOthers>
    </div>
    <GoTop></GoTop>
  </div>
</template>
<style scoped>
#model-middle-title {
  color: #00A1E9;
  font-size: 20px;
  margin-top: -8px;
  font-weight: 900;
  width: 100%;
  margin-bottom: 11px;
}
#model-middle-title > span{
  float: left;
  font-size: 24px;
  cursor: default;
}
#model-middle-title > span + span {
  clear: left;
  display: block;
  font-size: 18px;
}
#model-middle-title .input-group {
  width: 240px;
  float: right;
  border-radius: 20px;
}
#model-middle-title input {
  height: 36px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  font-weight: 100;
  background-color: #fff;
  font-size: 14px;
  padding-top: 7px;
  border: 3px solid #00A1E9;
}
#model-middle-title .input-group .input-group-btn {
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: #00A1E9;
  color: white;
  height: 36px!important;
  border: 0;
  width: 40px;
  font-size: 16px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
#model-middle-items #upload-time+span{
  display: inline-block;
}
#upload-time {
  margin-right: 10px;
}
#model-middle-items p:last-child {
  font-size: 12px;
}
#model-middle-items p:first-child {
  font-size: 16px;
}
#model-middle-items .pull-right img {
  height: 14px;
  margin-top: -5px;
}
#model-middle-items>img {
  float: left;
  height: 32px !important;
  border-radius: 14px;
  margin-right: 5px;
  margin-bottom: 35px;
  margin-top: 5px;
}
#model-middle-items {
  margin-top: 10px;
  background-color: #fff;
  border-bottom: 1px solid rgba(128, 128, 128, 0.5);
  color: #616161;
}
#load-more {
  margin-top: 10px;
  color: #95999b;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  margin-bottom: -12px;
  transition: 0.3s;
}
#load-more:hover {
  color: #00A1E9;
}
.file-name {
  font-weight: 600;
  color: #616161!important;
}
#no-such-files {
  margin-top: 30px;
  color: #757575;
  text-align: center;
  font-size: 18px;
  margin-bottom: 30px;
}
@media screen and (max-width:420px) {
  #model-middle-title .input-group, #wanted-middle-title .input-group {
    width: 100%;
    margin-top: 8px;
  }
}
</style>
<script>
import $ from 'jquery'
import PadOnOthers from '../../components/common/WantedLaunchPad/PadOnOthers.vue' // “发起悬赏”板块
import PersonPad from '../../components/common/PersonPad.vue' // 个人信息板块
import HotTags from '../../components/common/HotTags.vue' // “热门标签”板块
import HotFile from '../../components/common/HotFile.vue' // “热门文档”板块
import HomeworkPad from '../../components/common/HomeworkPad.vue' // “作业答案”板块，仅在“热门资源”页面使用
import GoTop from '../../components/GoTop.vue'
export default {
  name: 'Model', // 此页为模板页，有四个相似页面均使用此模板
  components: { HotFile, HomeworkPad, HotTags, PersonPad, PadOnOthers, GoTop },
  data () {
    return {
      Title: '', // 此页主题
      holder: '',
      keyword: '',
      ModelTitle: '此页主题',
      IsHotFile: false, // 用于确认此页面是否是“热门资源页面”
      files: [],
      RequestUrl: '',
      CurrentNum: 15,
      HasSearched: false,
      SearchUrl: '',
      CurrentPage: '',
      TagId: 0
    }
  },
  props: ['HasLoggedIn', 'PersonData'],
  /* created () {
  }, */
  watch: {
    '$route': 'CreatePage'
  },
  mounted () {
    this.CreatePage()
    document.body.scrollTop = 0
    document.documentElement.scrollTop = 0
  },
  methods: {
    CreatePage: function () {
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
      // 获得此页的名称
      this.CurrentPage = this.$route.name
      // 获得此页的标签的主题
      this.TagId = this.$route.query.Tag
      // 获得此页的主题
      this.Title = this.$route.query.PageTitle
      // 判断左上角板块应该放什么
      this.IsHotFile = false
      this.files = []
      $('#no-such-files').addClass('divhidden')
      $('#load-more').addClass('divhidden')
      $('#loader').removeClass('divhidden')
      this.HasSearched = false
      // 根据主题判断应该调用哪个获取文件数据的接口
      if (this.Title === 'hot') {
        this.RequestUrl = 'http://47.95.216.239/getHotList?num='
        this.SearchUrl = 'http://47.95.216.239/searchInHotList?num='
        this.ModelTitle = '热门资源'
        document.title = '热门资源-TWT学习平台'
        this.IsHotFile = true
      } else if (this.Title === 'homework') {
        this.RequestUrl = 'http://47.95.216.239/getHotHomeworkList?num='
        this.SearchUrl = 'http://47.95.216.239/searchInClassifiedFile?type=homework&num='
        this.ModelTitle = '作业答案'
        document.title = '作业答案-TWT学习平台'
      } else if (this.Title === 'exams') {
        this.RequestUrl = 'http://47.95.216.239/getHotQuesYearsList?num='
        this.SearchUrl = 'http://47.95.216.239/searchInClassifiedFile?type=ques_years&num='
        this.ModelTitle = '历年假题'
        document.title = '历年假题-TWT学习平台'
      } else if (this.Title === 'courseware') {
        this.RequestUrl = 'http://47.95.216.239/getHotClassPptList?num='
        this.SearchUrl = 'http://47.95.216.239/searchInClassifiedFile?type=class_ppt&num='
        this.ModelTitle = '教师课件'
        document.title = '教师课件-TWT学习平台'
      } else {
        this.ModelTitle = '根据标签筛选'
        // 若此页为“根据标签筛选文件页”
        $.ajax({
          async: true,
          type: 'GET',
          url: 'http://47.95.216.239/getTagsNameByID?id=' + this.TagId,
          dataType: 'json',
          success: (data) => {
            this.ModelTitle = data.data[0]
            document.title = this.ModelTitle + '-标签筛选'
          }
        })
        this.RequestUrl = 'http://47.95.216.239/getFilesByTag?tag=' + this.TagId + '&num='
      }
      // 根据页面主题更改搜索框中 placeholder
      this.holder = '\ue6ac在' + this.ModelTitle + '中搜索'
      // this.files = []
      $.ajax({
        async: true,
        type: 'GET',
        url: this.RequestUrl + this.CurrentNum,
        dataType: 'json',
        success: (data) => {
          this.files = data.data
          $('#loader').addClass('divhidden')
          if (data.data.length === 0) {
            $('#no-such-files').removeClass('divhidden')
          } else if (data.data.length === 15) {
            $('#load-more').removeClass('divhidden')
          }
        }
      })
    },
    UploadTime: function (data) {
      // 返回年月日 但并不知道 yy.mm.dd 与 yy/mm/dd 与 yy-mm-dd 的各自的优劣
      var YMD = data.file_upload_time.split(' ')[0].split('-')
      return YMD.join('/')
    },
    TypeWrap: function (data) {
      // 根据文件格式返回该格式对应的图标的路径以便渲染出 DOM
      // 不能用相对路径就很坑
      var TypeSrc = ''
      if (data.file_type === 'doc' || data.file_type === 'docx') {
        TypeSrc = '/static/FileImg/word.png'
      } else if (data.file_type === 'ppt' || data.file_type === 'pptx') {
        TypeSrc = '/static/FileImg/ppt.png'
      } else if (data.file_type === 'pdf') {
        TypeSrc = '/static/FileImg/pdf.png'
      } else if (data.file_type === 'xls') {
        TypeSrc = '/static/FileImg/xls.png'
      } else {
        TypeSrc = '/static/FileImg/default.png'
      }
      return TypeSrc
    },
    LoadMore: function () {
      this.CurrentNum += 15
      $('#loader').removeClass('divhidden')
      if (this.HasSearched === false) {
        $.ajax({
          async: true,
          type: 'GET',
          url: this.RequestUrl + this.CurrentNum,
          dataType: 'json',
          success: (data) => {
            $('#loader').addClass('divhidden')
            this.files = data.data
            if (data.data.length === this.CurrentNum) {
              $('#load-more').removeClass('divhidden')
            } else {
              $('#load-more').addClass('divhidden')
            }
          }
        })
      } else {
        $('#load-more').addClass('divhidden')
        $('#no-such-files').addClass('divhidden')
        $.ajax({
          async: true,
          type: 'GET',
          url: this.SearchUrl + this.CurrentNum + '&keyword=' + this.keyword,
          dataType: 'json',
          success: (data) => {
            if (data.data.length === 0) {
              $('#no-such-files').removeClass('divhidden')
              $('#load-more').addClass('divhidden')
            }
            if (data.data.length >= this.CurrentNum) $('#load-more').removeClass('divhidden')
            $('#loader').addClass('divhidden')
            this.files = data.data
          }
        })
      }
    },
    Upload: function () {
      this.$emit('upload')
    },
    Login: function () {
      this.$emit('login')
    },
    Search: function () {
      if (this.keyword === '') {
        alert('关键词不能为空')
      } else {
        this.files = []
        $('#load-more').addClass('divhidden')
        this.HasSearched = true
        $('#no-such-files').addClass('divhidden')
        this.CurrentNum = 15
        $('#loader').removeClass('divhidden')
        $.ajax({
          async: true,
          type: 'GET',
          url: this.SearchUrl + this.CurrentNum + '&keyword=' + this.keyword,
          dataType: 'json',
          success: (data) => {
            if (data.data.length === 0) {
              $('#no-such-files').removeClass('divhidden')
              $('#load-more').addClass('divhidden')
            }
            if (data.data.length >= this.CurrentNum) $('#load-more').removeClass('divhidden')
            $('#loader').addClass('divhidden')
            this.files = data.data
          }
        })
      }
    }
  }
}
</script>



