<template>
  <div class="card-content" id="hot-file">
    <div id="model-left-title">
      <router-link :to="{ path: '/mainpage', query: { PageTitle: 'hot' } }">
        <p id="to-hot-file">
          <span>热门资源</span>
        </p>
      </router-link>
    </div>
    <div class="spinner loader">
      <div class="rect1"></div>
      <div class="rect2"></div>
      <div class="rect3"></div>
      <div class="rect4"></div>
      <div class="rect5"></div>
    </div>
    <div class="model-item-left" v-for="file in files" :key="file.id">
      <img :src="TypeWrap(file)" alt="">
      <router-link :to="{ path: '/file', query: { id: file.id } }"><span>{{ file.file_title }}</span></router-link>
    </div>
  </div>
</template>
<style scoped>
#hot-file {
  margin-bottom: 10px;
}
.model-item-left a {
  color: #616161;
}
#model-tag-left button {
  margin-bottom: 8px;
  margin-left: 8px!important;
  margin-right: 0!important;
  background-color: #f8f8f8;
  color: #616161;
  border: 0;
  border-radius: 0;
}
#model-left-title {
  font-weight: 600;
  font-size: 16px;
}
#model-left-title p {
  border-left: 2px solid #00A1E9;
  text-decoration: none;
  padding-left: 5px;
  margin-bottom: 20px;
}
.model-item-left img {
  height: 20px;
  float: left;
  margin-right: 5px;
}
.model-item-left, #discussion-item-left {
  display: block;
  overflow: auto;
  zoom: 1;
  padding-top: 8px;
  padding-bottom: 8px;
}
#to-hot-file {
  color: #00A1E9!important;
}
</style>
<script>
import $ from 'jquery'
export default {
  // 用于“作业答案”、“历年假题”、“教师课件”三个页面左上角的板块
  name: 'HotFile',
  data () {
    return {
      files: []
    }
  },
  mounted () {
    this.files = []
    $.ajax({
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/getHotList?num=6',
      dataType: 'json',
      success: (data) => {
        this.files = data.data
        $('.loader').addClass('divhidden')
      }
    })
  },
  methods: {
    TypeWrap: function (data) {
      // 根据文件格式返回该格式对应的图标的路径以便渲染出 DOM
      // 不能用相对路径就很坑
      var TypeSrc = ''
      if (data.file_type === 'doc' || data.file_type === 'docx') {
        TypeSrc = '/static/FileImg/word.png'
      } else if (data.file_type === 'ppt' || data.file_type === 'pptx') {
        TypeSrc = '/static/FileImg/ppt.png'
      } else if (data.file_type === 'pdf') {
        TypeSrc = '/static/FileImg/pdf.png'
      } else if (data.file_type === 'xls') {
        TypeSrc = '/static/FileImg/xls.png'
      } else {
        TypeSrc = '/static/FileImg/default.png'
      }
      return TypeSrc
    }
  }
}
</script>



