<template>
  <div>
    <SearchBox v-on:getkeyword="get"></SearchBox>
    <div class="banner hidden-xs">
      <div class="banner-div card" @mouseleave="LeaveBanner">
        <div class="banner-div-left pull-left">
          <div class="spinner" id="loader">
            <div class="rect1"></div>
            <div class="rect2"></div>
            <div class="rect3"></div>
            <div class="rect4"></div>
            <div class="rect5"></div>
          </div>
          <ul class="banner-ul " id="banner-ul">
            <li v-for="FTag in TagTree" :key="FTag.id" :index="FTag.id" @mouseenter="Show($event)"><b><a>{{ FTag.name }}</a></b>></li>
          </ul>
        </div>
        <div class="banner-div-right pull-left">
          <div class="banner-right" v-for="FTag in TagTree" :key="FTag.id" :id="'banner-right-id'+FTag.id">
            <ul class="">
              <li v-for="STag in FTag.SonTag" :key="STag.id">
                <b @click="ShowTags($event)">{{ STag.name }}</b>
                <router-link v-if="SSTag.id!==9999" v-for="SSTag in STag.SonTag" :key="SSTag.id" :to="{ path: '/filterbytag', query: { Tag: SSTag.id } }"><span>{{ SSTag.name }}</span></router-link>
                <a v-for="SSTag2 in STag.SonTag" :key="SSTag2.id" v-if="SSTag2.id===9999" href="javascript:void(0);"><span>无子标签</span></a>
              </li>
            </ul>
          </div>
        </div>
        <div class="banner-person-board">
          <div class="avatar-bg">
            <img src="../../assets/img/noimg.png" alt="" id="avatar" class="center-block">
            <div id="logout-container">
              <div class="btn-group">
                <p id="user-name" data-toggle="dropdown"><span v-if="!HasLoggedIn" @click="LogIn">登录</span><span v-if="HasLoggedIn">avfav1235</span></p>
                <ul class="dropdown-menu" role="menu">
                  <li>
                    <a @click="Logout">退出登录</a>
                  </li>
                </ul>
              </div>
              <p class="credit" v-if="HasLoggedIn">积分：<span id="credit">{{ this.PersonData.coin }}</span></p>
            </div>
          </div>
          <div class="personal-entrance">
            <div v-if="!HasLoggedIn" id="empty-block"></div>
            <p class="personal-center" >
              <img src="../../assets/img/personal.png" alt="">
              <span v-if="!HasLoggedIn" style="cursor: not-allowed">个人中心</span>
              <router-link :to="{ path: '/user' }" v-if="HasLoggedIn">个人中心</router-link>
            </p>
            <button v-if="HasLoggedIn" type="button" class="btn btn-primary upload center-block" @click="UploadFile">
              <img src="../../assets/img/upload.png" alt="">
              上传文件
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="body-center clearfix">
      <!-- 四个主要板块 -->
      <MainBoard :BoardIndex="0"></MainBoard>
      <MainBoard :BoardIndex="1"></MainBoard>
      <MainBoard :BoardIndex="2"></MainBoard>
      <MainBoard :BoardIndex="3"></MainBoard>
      <!-- 悬赏板块 -->
      <WantedBoard v-on:postreward="PostReward"></WantedBoard>
      <!-- 讨论版块 -->
      <DiscBoard></DiscBoard>
    </div>
  </div>
</template>
<style>
a {
  text-decoration: none!important;
}
.banner {
  width: 100%;
  max-width: 1200px;
  height: 360px;
  margin: 10px auto;
  box-sizing: border-box;
  padding-left: 5px;
  padding-right: 5px;
}
.banner-div {
  margin: 0 auto;
  margin-top: 0px;
  max-width: 1200px;
  height: 352px;
  background: url(../../assets/img/banner.png) no-repeat;
  background-size: 1000px 500px;
  background-position: center;
  background-size: cover;
}
.banner-div-left {
  width: 254px;
  height: 100%;
  background-color: #f7fbfe;
  font-size:20px;
  color: #a8abac;
}
#loader {
  height: 110px;
  margin: 120px auto;
  margin-bottom: 90px!important;
}
.banner-ul {
  position: relative;
  box-sizing: border-box;
  padding-top: 20px;
  padding-left: 27px;
}
.banner-ul li {
  width: 191px;
  list-style: none;
  margin-top: 28px;
  border-bottom: 1px solid #a8abac;
  box-sizing: border-box;
  padding-left: 10px;
  padding-bottom: 10px;
}
.banner-ul li:last-child {
  border: none;
}
.banner-ul>:first-child {
  position: relative;
  margin-top: 10px!important;
}
.banner-div li b>a {
  color:#a8abac;
}
.banner-right {
  display: none;
  position: absolute;
  z-index: 5555;
  background-color:#f7fbfe;
  opacity: 0.9;
  height: 352px;
  max-width: 420px;
  padding-right: 40px;
}
.banner-right a {
  box-sizing: border-box;
  z-index: 6667!important;
  background-color: white!important;
  opacity: 1!important;
  width: 160px;
  float: left;
  overflow: hidden;
  margin: 0;
  box-shadow: 1px 1px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 4px 0 rgba(0,0,0,.12);
}
.banner-right li {
  margin-top: 20px;
  display: inline-block;
  width: 120px;
  height: 30px;
}
.banner-right b {
  position: relative;
  font-size: 16px;
  color: #00A1E9;
  margin-right: 10px;
  cursor: default;
  margin-bottom: 0;
  z-index: 0;
}
.banner-right span {
  height: 28px;
  font-size: 15px;
  color:#252525;
  z-index: 6667!important;
  background-color: white!important;
  opacity: 1!important;
}
.banner-right>ul {
  display: flex;
  flex-direction: column;
  height: 352px;
  width: 420px;
  flex-wrap: wrap;
}
.show {
  display: block!important;
  z-index: 0;
}
.show li a {
  height: 0;
}
.showcolor {
  color:#00A1E9 !important;
}
.showcolor b>a {
  color:#00A1E9 !important;
}
.banner-div-right li {
  list-style: none;
}
#empty-block {
  height: 20px;
}
.banner-person-board {
  float: right !important;
  width: 225px;
  height: 100%;
  background-color: #f7fbfe;
  font-size:20px;
  color:#a8abac;
}
.avatar-bg {
  height: 225px;
  background-color: #00A1E9;
  padding-top: 10px;
}
#user-name {
  font-size: 25px;
  color: white;
  margin: 5px auto;
  text-align: center;
}
#user-name span {
  cursor: pointer;
}
.personal-entrance img {
  height: 18px;
}
#avatar {
  margin-top: 20px;
  box-sizing: border-box;
  height: 120px;
  width: 120px;
  border-radius: 60px;
  border: 5px solid white;
  -moz-border-radius: 60px;
  -webkit-border-radius: 60px;
}
.personal-center {
  font-size: 20px;
  color: #252525;
  text-align: center;
  margin-top: 20px;
}
.personal-center a {
  color: #252525;
}
.personal-center img {
  height: 16px;
}
.upload {
  border: 0;
  font-size: 20px;
  background-color: #01aff8;
  margin-top: 10px
}
.credit {
  font-size: 15px;
  color: white;
  text-align: center;
}
#loader-1, #loader-2, #loader-3, #loader-4, #loader-5 {
  margin-left: 90px;
  height: 100px;
  margin-top: 126px;
  margin-right: 50px;
}
.add-tag-height {
  height: 26px!important;
  padding-top: 3px;
  padding-bottom: 3px;
  padding-left: 5px;
  margin-left: 10px!important;
  position: relative
}
#logout-container {
  display: block;
  width: 100%;
}
#logout-container > .btn-group {
  display: block;
}
#logout-container > .btn-group > .dropdown-menu {
  min-width: 225px!important;
  text-align: center;
  cursor: pointer;
}
@media screen and (min-width:1220px) {
  .body-center {
    margin: 0 auto;
    margin-top: 0;
    max-width: 1200px;
  }
}
@media screen and (max-width: 1220px) and (min-width:992px) {
  .body-center {
    margin: 0 auto;
    width: 910px;
  }
}
@media screen and (min-width: 780px) and (max-width:992px) {
  .body-center {
    margin: 0 auto;
    width: 760px;
  }
}
@media screen and (max-width:780px) {
  .body-center {
    width: 100%;
    height: auto;
  }
  .body-center-sondiv:first-child {
    margin-top: 10px!important;
  }
}
</style>
<script>
import SearchBox from '../../components/common/SearchBox.vue' // 搜索框
import MainBoard from '../../components/common/MainBoard.vue' // 四个主要板块
import WantedBoard from '../../components/common/SubMainBoard/WantedBoard.vue' // 悬赏中心板块
import DiscBoard from '../../components/common/SubMainBoard/DiscBoard.vue' // 讨论中心板块
import $ from 'jquery'
export default {
  components: { SearchBox, MainBoard, WantedBoard, DiscBoard },
  data () {
    return {
      keyword: '', // 搜索关键词
      PageIndex: [ 0, 1, 2, 3 ]
    }
  },
  props: ['TagTree', 'HasLoggedIn', 'PersonData'],
  mounted () {
    if (this.TagTree.length !== 0) $('#loader').addClass('divhidden')
    if (this.TagTree.length === 0) $('#loader').removeClass('divhidden')
  },
  methods: {
    get: function (arg) {
      // 接受来自子组件 SearchBox.vue 的参数
      this.keyword = arg
    },
    Show: function ($event) {
      var dom = $event.currentTarget
      $(dom).addClass('showcolor')
      $(dom).siblings('a').toggleClass('add-tag-height')
      $('.banner-right').not('#banner-right-id' + $(dom).attr('index')).find('a').removeClass('add-tag-height')
      $('.banner-ul').children().not(dom).removeClass('showcolor')
      $('#banner-right-id' + $(dom).attr('index')).addClass('show')
      $('.banner-div-right').children().not('#banner-right-id' + $(dom).attr('index')).removeClass('show')
    },
    LeaveBanner: function () {
      $('.banner-right').find('a').removeClass('add-tag-height')
      $('.banner-ul').children().removeClass('showcolor')
      $('.banner-div-right').children().removeClass('show')
    },
    LogIn: function () {
      this.$emit('login') // 向 App.vue 传递登录消息
    },
    Logout: function () {
      this.$emit('logout')
    },
    UploadFile: function () {
      this.$emit('upload')
    },
    PostReward: function () {
      this.$emit('postreward')
    },
    ShowTags: function ($event) {
      var dom = $event.currentTarget
      $(dom).siblings('a').toggleClass('add-tag-height')
      $(dom).parent('li').siblings('li').children('a').removeClass('add-tag-height')
    },
    GetTagTree: function () {
      if (this.TagTree.length !== 0) $('#loader').addClass('divhidden')
    }
  },
  watch: {
    TagTree: 'GetTagTree'
  }
}
</script>



