  margin-bottom: 8px;
  margin-left: 8px!important;
  margin-right: 0!important;
  background-color: #f8f8f8;
  color: #616161;
  border: 0;
  border-radius: 0;
}
#model-left-title>p {
  border-left: 2px solid #00A1E9;
  padding-left: 5px;
  margin-bottom: 20px;
}
.tag a {
