<template>
  <div id="launch-wanted" class="card">
    <p>
      找不到想要的文档？
    </p>
    <router-link :to="{ path: '/wanted' }">
      <button class="btn-primary btn btn-warning wanted-button center-block" v-if="!IsWanted">
        去悬赏
      </button>
    </router-link>
    <button class="btn-primary btn btn-warning wanted-button center-block" v-if="IsWanted" @click="PostReward">
      发起悬赏
    </button>
  </div>
</template>
<style scoped>
#launch-wanted {
  float: left;
  background-color: #fff;
  width: 100%;
  height: 160px;
  margin-bottom: 10px;
}
#launch-wanted button {
  height: 36px;
  width: 128px;
  padding-top: 4px;
}
#launch-wanted>p {
  margin-top: 30px;
  text-align: center;
  font-size: 17px;
  color: #616161;
  margin-bottom: 30px;
}
.wanted-button {
  width: 160px;
  height: 50px;
  font-size: 20px;
  color: white!important;
}
.wanted-button a {
  color: white!important;
}
</style>
<script>
export default {
  // 用于模板页、“悬赏中心”页面右下角的发起悬赏板块
  name: 'PadOnOthers',
  data () {
    return {
      CurrentPage: '',
      IsWanted: false // 判断此页是否为“悬赏中心”页面，来确定发起悬赏用哪个按钮
    }
  },
  created () {
    this.PageInit()
  },
  methods: {
    PageInit: function () {
      this.CurrentPage = this.$route.name
      if (this.CurrentPage === 'Wanted') this.IsWanted = true
    },
    PostReward: function () {
      this.$emit('postreward')
    }
  },
  watch: {
    '$route': 'PageInit'
  }
}
</script>



