<template>
  <div id="reward-center" class="body-center-sondiv">
    <div class="card-content">
      <div class="body-center-every-title">
        <router-link :to="{ path: '/wanted' }">
          <b>悬赏中心</b>
        </router-link>
        <span id="wanted-center-launchpad">
          <span>
            找不到文档？
          </span>
          <button class="btn-primary btn btn-warning reward" @click="PostReward">
            发起悬赏
          </button>
        </span>
      </div>
      <div class="wanted-item" v-for="file in MostWanted" :key="file.id">
        <div class="wanted-title clearfix">
          <span class="wanted-tag">
            <i class="iconfont">&#xe6c5; </i>
            <span>{{ file.tag_name }}</span>
          </span>
          <label>悬赏</label>
          <router-link :to="{ path: '/wanteddetail', query: { id: file.id } }">{{ file.file_name }}</router-link>
        </div>
        <div>
          <span class=" wanted-tips">
            <b>赏金：</b>
            <span>{{ file.coin }}</span>
          </span>
          <div class="wanted-launch-time">
            <span>
              {{ file.start_time.split(' ')[0] }}
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
.body-center-sondiv{
  background-color: transparent;
  border-radius: 0px;
  float: left;
  padding-left: 5px;
  padding-right: 5px;
  margin-bottom: 10px;
  border-radius: 10px;
}
#reward-center .body-center-every-title b {
  color: #dd5067!important;
  transition: 0.3s
}
#reward-center .body-center-every-title b:hover {
  color: #da7888!important;
}
#wanted-center-launchpad {
  float: right;
}
#wanted-center-launchpad {
  float: left;
}
#wanted-center-launchpad>span {
  color: #a8abac;
  font-size: 20px;
}
.reward {
  width: 90px;
  height: 30px;
  font-size: 14px;
  padding-top: 5px;
  border: none;
}
.reward a {
  color: white!important;
}
.wanted-item {
  background-color: white;
  padding-top: 26px;
  padding-bottom: 10px;
}
.wanted-item {
  height: 100px;
  padding-top: 25px;
}
.wanted-title label {
  font-size: 15.21px;
  border: 1px solid #dd5067;
  color:#dd5067;
  border-radius: 8px;
  padding: 0px 10px;
  margin-right:10px;
  padding-left: 5px!important;
  padding-right: 5px!important;
}
.wanted-title b {
  font-size:15.21px ;
  color:#a8abac;
}
.wanted-tag {
  float: right;
  margin-left: 8px;
}
.wanted-tips b {
  margin-left: 10px;
  font-size: 15px;
  color:#a8abac;
}
.wanted-tips span {
  font-size: 15px;
  color:#e47e8d;
}
.wanted-tips i {
  margin-left: 70px;
}
.wanted-launch-time {
  float: right;
  margin-top: 3px;
}
.body-center-every-title b{
  float: left;
  margin-right: 15px;
  margin-bottom: 5px;
  cursor: default;
  color:#00A1E9!important;
  transition: 0.3s;
}
.body-center-every-title {
  background-color: white;
  font-weight: 200;
  font-size:22px;
  color:#00A1E9!important;
  margin-bottom: 8px;
  line-height: 30px;
  padding-bottom:3px;
  margin-top: -8px;
  overflow: auto;
  zoom: 1;
}
.wanted-title a {
  color: #95999b;
  font-size: 15px;
  font-weight: 600;
}
@media screen and (min-width:1220px) {
  .body-center-sondiv {
    width: 600px;
  }
}
@media screen and (max-width: 1220px) and (min-width:992px) {
  .body-center-sondiv {
    width: 455px;
  }
}
@media screen and (min-width: 780px) and (max-width:992px) {
  .body-center-sondiv {
    height: auto;
    width: 380px;
  }
}
@media screen and (max-width:780px) {
  .body-center-sondiv {
    margin-top: 5px;
    width: 100%;
    margin-bottom: 5px!important;
  }
}
</style>
<script>
import $ from 'jquery'
export default {
  // 主页下方的“悬赏中心”板块
  name: 'WantedBoard',
  data () {
    return {
      MostWanted: []
    }
  },
  mounted () {
    document.body.scrollTop = 0
    document.documentElement.scrollTop = 0
    $.ajax({
      async: true,
      type: 'GET',
      url: 'http://47.95.216.239/getHotReward?num=2',
      dataType: 'json',
      success: (data) => {
        this.MostWanted = data.data
      }
    })
  },
  methods: {
    PostReward: function () {
      this.$emit('postreward')
    }
  }
}
</script>



