<template>
  <!-- 搜索页面的“发起悬赏”板块 -->
  <div id="searched-launch">
    <div class="card-content">
      <p>
        找不到想要的文档？
      </p>
      <router-link :to="{ path: '/wanted' }">
        <button class="btn-primary btn btn-warning wanted-button center-block">
          去悬赏
        </button>
      </router-link>
    </div>
  </div>
</template>
<style scoped>
#searched-launch p {
  margin-top: 40px;
  text-align: center;
  font-size: 20px;
  color: #616161;
  margin-bottom: 50px;
}
#searched-launch {
  padding-left: 5px;
  padding-right: 5px;
  margin-top: 10px;
  margin-bottom: 10px;
}
#searched-launch button {
  display: block;
}
.wanted-button {
  width: 160px;
  height: 50px;
  font-size: 20px;
}
.wanted-button a {
  color: white!important;
}
@media screen and (min-width:1220px) {
  #searched-launch {
    width: 390px;
    float: left;
    padding: 0!important;
  }
  #searched-launch p {
    margin-top: 40px;
    text-align: center;
    font-size: 20px;
    color: #616161;
    margin-bottom: 70px;
  }
  #searched-launch button {
    display: block;
    margin: 10px auto;
    font-size: 17px;
  }
}
@media screen and (max-width: 1220px) and (min-width:992px) {
  #searched-launch {
    width: 100%;
    float: left;
    height: 246px;
  }
}
@media screen and (min-width: 767px) and (max-width:992px) {
  #searched-launch {
    width: 100%;
    float: left;
  }
}
@media screen and (max-width: 767px) {
  #searched-launch p {
    margin-top: 12px;
    margin-bottom: 24px;
  }
  .wanted-button {
    height: 45px;
  }
}
</style>
<script>
export default {
  name: 'PadOnSearched'
}
</script>



