import Vue from 'vue'
import Router from 'vue-router'
// 下面引入的是页面，组件在各个页面中引入
import Index from '@/page/Index/Index.vue'
import Searched from '@/page/Searched/Searched.vue'
import Model from '@/page/Model/Model.vue'
import WantedMain from '@/page/Wanted/WantedMain.vue'
import Discussion from '@/page/Discussion/Discussion.vue'
import Personal from '@/page/Personal/Personal.vue'
import WantedDetail from '@/page/Wanted/WantedDetail.vue'
import FileDetail from '@/page/Searched/FileDetail.vue'
import DiscDetail from '@/page/Discussion/DiscussionDetail.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      meta: {
        title: '天外天学习平台'
      }
    },
    {
      // 采用 query 方式查询关键词
      path: '/search',
      name: 'Searched',
      component: Searched,
      meta: {
        title: '搜索结果-TWT学习平台'
      }
    },
    {
      // 采用 query 方式切换各页面
      path: '/mainpage',
      name: 'Model',
      component: Model
      // 页面的 title 在 Model.vue 里面根据页面主题修改
    },
    {
      // 采用 query 方式切换各页面
      path: '/filterbytag',
      name: 'Tag',
      component: Model
      // 页面的 title 在 Model.vue 里面根据页面标签修改
    },
    {
      // 悬赏页面，搜索时采用 query 方式传值
      path: '/wanted',
      name: 'Wanted',
      component: WantedMain,
      meta: {
        title: '悬赏中心-TWT学习平台'
      }
    },
    {
      path: '/wanteddetail',
      name: 'WantedDetail',
      component: WantedDetail,
      meta: {
        title: '悬赏详情-TWT学习平台'
      }
    },
    {
      path: '/discussion',
      name: 'Discussion',
      component: Discussion,
      meta: {
        title: '讨论中心-TWT学习平台'
      }
    },
    {
      path: '/user',
      name: 'Personal',
      component: Personal,
      meta: {
        title: '个人中心-TWT学习平台'
      }
    },
    {
      path: '/file',
      name: 'FileDetail',
      component: FileDetail,
      meta: {
        title: '文件详情-TWT学习平台'
      }
    },
    {
      path: '/discdetail',
      name: 'DiscDetail',
      component: DiscDetail,
      meta: {
        title: '讨论详情-TWT学习平台'
      }
    }
  ]
})



